import { makeAutoObservable, flow, runInAction } from "mobx";
import isEmpty from "lodash/isEmpty";
import get from "lodash/get";
import { RequestService } from "services/RequestService";

import { getExceptionMessage } from "utils/errors";

import { observable } from 'mobx'

import { IPageableListDataSource } from "./interfaces/IPageableListDataSource"
import { IInvoiceDto } from "./interfaces/IInvoiceDto";
import { IInvoiceListFilter } from "./interfaces/IInvoiceListFilter";

import { CEXPAY_PAYTOOLS_INVOICE_API_URL } from "../../constants";
import {MerchantStore} from "./MerchantStore";
import {IMerchantStore} from "./interfaces/IMerchantStore";
import moment from "moment";

const LIMIT = 10;
  
export class InvoiceListStore implements IPageableListDataSource<IInvoiceDto> {

  private readonly request: RequestService;

  private readonly merchantStore: IMerchantStore;

  public needReload: boolean = false;

  public loading: boolean = false;

  public error: string = "";

  private data: any = {};

  private pageSize: number;

  get totalCount() : number {
    return this.data?.totalCount;
  }
  
  get all() {
    return this.data?.invoices ?? new Array<IInvoiceDto>();
  }

  constructor( request: RequestService, merchantStore: IMerchantStore, pageSize: number ) {
    makeAutoObservable(this);

    this.request = request;
    this.pageSize = pageSize;
    this.merchantStore = merchantStore;
  }

  async fetchData( filter : IInvoiceListFilter ) {

    var filterDto =
    {
        merchantId: this.merchantStore.current?.id ?? 0,
        beforeId: '',
        fromDate: filter.fromDate == '' ? null : moment(filter.fromDate).format(),
        toDate: filter.toDate == '' ? null : moment(filter.toDate).format(),
        states: filter.states,
        currencies: filter.currencies,
        sort: filter.sortby,
        dir: filter.sortDirection,
        start: this.pageSize * (filter.page == '' ? 0 : (parseInt( filter.page ) - 1) ),
        limit: this.pageSize,
        search: filter.search
    };

     const res = await this.request.post( `${CEXPAY_PAYTOOLS_INVOICE_API_URL}invoice/list`, filterDto );
    
     runInAction(() => {
       if (res?.data) {
         this.data = res.data;
       }
     });
  }

  fetch = flow(function* (this: InvoiceListStore, filter : IInvoiceListFilter) {

    this.loading = true;
    this.error = "";

    try {
      yield Promise.all([
        this.fetchData(filter)
      ]);
    } catch (error) {
      this.error = getExceptionMessage(error);
    } finally {
      this.loading = false;
    }
  });
}
