import { IObservableArray, makeAutoObservable, observable } from "mobx";

import { IRootStore } from "./interfaces/IRootStore";
import { IAuthStore } from "./interfaces/IAuthStore";

import { DummyAuthStore } from "./DummyAuthStore";

import { DummyMerchantStore } from "./DummyMerchantStore";
import { DummyInvoiceListStore } from "./DummyInvoiceListStore";
import { DummyInvoiceStore } from "./DummyInvoiceStore";
import { DummyProfileStore } from "./DummyProfileStore";
import { DummyResourceStore } from "./DummyResourceStore"
import { IPageableListDataSource } from "./interfaces/IPageableListDataSource";
import { IInvoiceDto } from "./interfaces/IInvoiceDto";
import { IInvoice } from "./interfaces/IInvoice";
import { IMerchantStore } from "./interfaces/IMerchantStore";
import { IEntityDataSource } from "./interfaces/IEntityDataSource";
import { IProfileStore } from "./interfaces/IProfileStore";

import dummyInvoiceListMock from './mock/invoicelist.json'
import { IResourceStore } from "./interfaces/IResourceStore";

export class DummyRootStore implements IRootStore {

  public static readonly PageSize: number = 20;

  private readonly dataSource: IObservableArray<IInvoiceDto> = observable<IInvoiceDto>( dummyInvoiceListMock );

  public readonly authStore: IAuthStore;

  public readonly merchantStore: IMerchantStore;

  public readonly invoiceListStore: IPageableListDataSource<IInvoiceDto>;

  public readonly invoiceStore: IEntityDataSource<IInvoice>;

  public readonly profileStore: IProfileStore;

  public readonly resourceStore: IResourceStore;

  constructor() {

    makeAutoObservable(this);

    this.authStore = new DummyAuthStore();

    this.resourceStore = new DummyResourceStore();

    this.merchantStore = new DummyMerchantStore();

    this.profileStore = new DummyProfileStore( this.merchantStore );

    this.invoiceStore = new DummyInvoiceStore( this.dataSource, this.merchantStore, this.profileStore  );

    this.invoiceListStore = new DummyInvoiceListStore( this.dataSource, this.merchantStore, DummyRootStore.PageSize );

    this.onAfterBootstrap();
  }

  onAfterBootstrap() {
    // Dummy
  }
}
