import React, {FC, useEffect, useState} from "react";
import {observer} from "mobx-react-lite";

import { routes } from "routes";

import { Route, Switch } from "react-router-dom";

import { InvoicingSettings } from "pages/InvoicingSettings";
import { Merchant } from "pages/Merchant"

export const Settings : FC = observer(() => {

    return (
        <Switch>
          <Route exact={true} path={routes.settings} component={Merchant} />
          <Route exact={true} path={routes.settingsMerchants} component={Merchant} />
          <Route path={routes.settingsUsers} component={InvoicingSettings} />          
          <Route path={routes.settingsInvoicing} component={InvoicingSettings} />
        </Switch>
    );
});
