import {FC, useEffect, useState} from "react";
import { observer } from "mobx-react-lite";
import { useHistory, useRouteMatch } from "react-router-dom";

import { Error } from "components/Errors/Error";
import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";
import { Panel } from "./Panel";

import {Container} from "../../components/pages/Invoices/Container";
 
import {Header} from "../../components/pages/Invoices/Header"
import {InvoicesTopMenu} from "../../components/pages/Invoices/InvoicesTopMenu"

import {InvoicesToolbar} from './InvoicesToolbar';
import { defaultInvoiceId } from "services/StoreService/interfaces/IInvoice";
import { InvoiceState } from "services/StoreService/InvoiceStates";
import { useFlashMessage } from "hooks";
import { getExceptionMessage } from "utils/errors";

export const InvoiceEdit : FC<any> = observer(() => {

    const [infoMessage, flashInfoMessage] = useFlashMessage(3000);
    const [errorMessage, flashErrorMessage] = useFlashMessage(5000); 

    // Через эту переменную передаёем события нажатия кнопки "Сохранить".
    const [ saveCounter, setSaveCounter ] = useState<number>(0);

    const { invoiceStore } = useStore();
    const { params } = useRouteMatch<any>();

    const history = useHistory();
    
    useEffect(() =>
    {
        invoiceStore.fetch(params?.id);

    }, [params?.id]);
 
    useEffect(() =>
    {
        // Если сохранили новый инвойс, то в строке адреса браузера обновляем текущий адрес.
        if( !invoiceStore.loading &&
            params?.id == defaultInvoiceId &&
            invoiceStore.instance?.key != defaultInvoiceId && invoiceStore.instance?.key != null ) {

                const url = history.location.pathname;                
                history.replace( url.replace( defaultInvoiceId, invoiceStore.instance?.key ) );
            }
    }, [invoiceStore.loading]);


    const executeAction = async (action: string) => {

        try
        {
            switch (action)
            {
                case "publish":
                    await invoiceStore.publish();
                    flashInfoMessage("Have done");
                    break;
                case "resend":
                    await invoiceStore.send();
                    flashInfoMessage("Have done");
                    break;
                case "cancel":
                    await invoiceStore.cancel();
                    flashInfoMessage("Have done");
                    break;                    
                case "save":
                    setSaveCounter(saveCounter + 1);
                    break;                    
            }

            

        } catch (ex) {

            flashErrorMessage( 'Error: ' + getExceptionMessage(ex) );
        }
    } 

    const canExecuteAction = (action: string) : boolean => {

        const availableActions = InvoiceState.getAvailableActions( invoiceStore?.instance.state );
        return !invoiceStore.loading && availableActions.includes( action );        
    } 


    return (
        <>
            <Header title={(params?.id == defaultInvoiceId) ? 'New invoice' : ('Invoice #' + invoiceStore.instance.number)} 
                    isBackButtonVisiable={true}/>
            <InvoicesTopMenu />
            <Container>
                <InvoicesToolbar eventSource={invoiceStore.loading} // Эта привязка сделана, чтобы при изменении loading пересчитался canExecute (т.е. чтобы обновились состояния кнопок)
                                 executeAction={(action: string) => executeAction(action)}
                                 canExecuteAction={(action: string) : boolean => canExecuteAction(action)}
                                 errorMessage={errorMessage} infoMessage={infoMessage}
                                 />
                <Error error={invoiceStore.error}>
                    <Loader loading={invoiceStore.loading}>
                        <Panel invoice={invoiceStore.instance} fireSaveEvent={saveCounter} 
                               pushInfoMessage={(msg) => flashInfoMessage(msg)} 
                               pushErrorMessage={(msg) => flashErrorMessage(msg)} />    
                    </Loader>
                </Error> 
            </Container>
        </>
    );


});

