import { useEffect, useState } from "react";
import { FC } from "react";
import { observer } from "mobx-react-lite";
import { useHistory } from "react-router-dom";

import { Error } from "components/Errors/Error";
import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";
import { useQuery } from "hooks/useQuery";
import { useQueryFilter } from "hooks/useQueryFilter";

import {Header} from "../../components/pages/Invoices/Header"
import {InvoicesTopMenu} from "../../components/pages/Invoices/InvoicesTopMenu"

import {InvoicesToolbar} from './InvoicesToolbar';
import {Panel} from './Panel';

import {Container} from "../../components/pages/Invoices/Container";
import { routes } from "routes";
import { defaultInvoiceId } from "services/StoreService/interfaces/IInvoice";
import { InvoiceState } from "services/StoreService/InvoiceStates";

export const InvoiceList : FC<any> = observer( () => {

    // Счётчик срабатываний useEffect. Только если 
    const [pageLoadingCounter, setPageLoadingCounter] = useState<number>( 0 );

    const { invoiceListStore, profileStore } = useStore();
    const queryString = useQuery().toString();
    const queryFilter = useQueryFilter();
  
    useEffect( () => {
        invoiceListStore.fetch(queryFilter);
        invoiceListStore.needReload = false;
    }, [queryString]);
  
    useEffect( () => {

      setPageLoadingCounter( pageLoadingCounter+1 );

        // Если коллекция отмечена для обновления, то выполняем перезагрузку данных.
        if( invoiceListStore.needReload ) {
            invoiceListStore.needReload = false;          
            
            invoiceListStore.fetch(queryFilter);
        }
    }, []);

    const history = useHistory();

    const executeAction = async (action: string) => {

      try
      {
          switch (action)
          {
              case "create":
                  const url = routes.invoiceEdit.replace( ":id", defaultInvoiceId.toString() );
                  history.push( url );        
                  break;
          }

      } catch (err) {
          console.log(err);
      }
    } 

    const canExecuteAction = (action: string) : boolean => {

      const availableActions = InvoiceState.getAvailableActions(-1);
      return availableActions.includes( action ) && profileStore?.isValid();
    } 


    return (
      <>
        <Header title="Invoices" isBackButtonVisiable={false}/>
        <InvoicesTopMenu isSearchStringVisible={true}/>
        <Container>
          <InvoicesToolbar executeAction={(action: string) => executeAction(action)}
                           canExecuteAction={(action: string) : boolean => canExecuteAction(action)}
                           errorMessage={!profileStore?.isValid() ? `${profileStore?.validationError} ${profileStore?.errorResolution}` : undefined}
                                   />
          <Error error={invoiceListStore.error}>
            <Loader loading={(invoiceListStore.loading && (pageLoadingCounter==0)) || profileStore.loading}>
              <Panel />
            </Loader>
          </Error>
        </Container>
      </>
    );
  });        
