import { useStore } from 'providers/StoreProvider';
import React, {FC, useState} from 'react'
import {useDropzone} from 'react-dropzone'

import { Control, Controller, useController, useFormContext } from "react-hook-form"
import { defaultInvoiceId } from 'services/StoreService/interfaces/IInvoice';

import { IResourceInfoDto } from 'services/StoreService/interfaces/IResourceStore';


import styles from './LogoDropzone.module.scss'

export interface ILogoDropzone {
  control: any;
  fieldName: string;
}
 
// Контрол отображения логотипа мерчанта с возможностью загрузки (или выбором D&D) картинок.
export const LogoDropzone: FC<ILogoDropzone> = ( {control, fieldName } ) => {
  
  const {
    field: { onChange, onBlur, name, value, ref },
  } = useController({
    name: fieldName,
    control,
    rules: { required: true },
    defaultValue: "",
  });

  const [ files, setFiles ] = useState( Array<any>() );

  const { resourceStore } = useStore();

  if( files.length == 0 && value != null && value != '' ) {

    if( value == defaultInvoiceId ) {
      setFiles( [ { name: value, preview: "" } ] );
    }
    else
    {
      const preview = resourceStore.getResourceUrl( value );
      if( preview != null && preview != '' ) {
        const newFiles = [ { name: value, preview: preview } ];
        setFiles( newFiles );
      }
    }
  }

  const {getRootProps, getInputProps} = useDropzone({
    accept: 'image/*',  
    multiple: false,
    maxFiles: 1,
    onDrop: acceptedFiles => { 

      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach( (file: any) => URL.revokeObjectURL(file.preview));

      const fileWithPreview = acceptedFiles.map( (file: any) => Object.assign( file, { preview: URL.createObjectURL(file) } ) );
      setFiles( fileWithPreview );

      resourceStore.uploadFiles( acceptedFiles )
                  .then( (ids: Array<IResourceInfoDto>) => {
                    onChange( ids[0].uuid );
                  } );
    }
  });

  const thumbs = files.map( (file: any) => (
    <div className={styles.thumb} key={file.name}>
      <div className={styles.thumbInner}>
        {file.preview != "" ? <img src={file.preview} className={styles.zoneImg}/> : null}        
      </div>
    </div>
  ));

//  useEffect(() => {
//    // Make sure to revoke the data uris to avoid memory leaks
//    files.forEach( (file: any) => URL.revokeObjectURL(file.preview));
//  }, [files]);

  return (
    
        <section className={styles.container}>
          <div className={styles.dropzone} {...getRootProps()}>
            <input {...getInputProps()} />
            <input type="hidden" name={name} value={value ?? ''} onChange={onChange} onBlur={onBlur} />
            <aside>
              {thumbs}
            </aside>
            <p>Drag 'n' drop logotype file here, or click to select files</p>
          </div>
        </section>    
 
  ) 
};
