import { FC } from "react";
import * as Nav from "../Nav"

import styles from "./InvoicesTopMenu.module.scss"

import { routes } from "../../../../routes";

import { TopMenu } from "../../../TopMenu"
import { useQuery } from "hooks/useQuery";


export const InvoicesTopMenu: FC<any> = ( {isSearchStringVisible} ) => {

    const querySearch = useQuery().toSearch();

    return (
        <TopMenu isSearchStringVisible={isSearchStringVisible}>
            <Nav.Area>
                <Nav.Link className={styles.TopMenuButtonTopLine}
                          exact={false} to={`${routes.invoices}${querySearch}`}
                          isActive={(match, { pathname }) =>
                                                             pathname === routes.index ||
                                                             pathname === routes.invoices ||
                                                             !!match} >
                Invoices
                </Nav.Link>
                {/*<Nav.Link className={styles.TopMenuButtonTopLine}*/}
                {/*          exact={false} to={`${routes.orders}${querySearch}`}>*/}
                {/*Orders*/}
                {/*</Nav.Link>*/}
            </Nav.Area>   
        </TopMenu>
    );
  };