import {FC, useState} from "react";

import {observer} from "mobx-react-lite";
import {Button} from "components/Forms/Button";
import {Toolbar} from "components/pages/Invoices/Toolbar";

import styles from './InvoicesToolbar.module.scss'

import { IActionToolbar } from "components/IActionToolbar"
import { useFlashMessage } from "hooks";

import { Error as ErrorControl } from "components/Forms/Error";
import { Info } from "components/Forms/Info";

export const InvoicesToolbar : FC<IActionToolbar> = observer( ( {executeAction, canExecuteAction = (action: string) : boolean => true, errorMessage, infoMessage } ) => {
      
    return (
        <div>
            <div className={styles.invoiceListToolbar}>                
                <div></div>
                <Toolbar>
                    <Button disabled={!canExecuteAction("cancel")}
                            onClick={() => executeAction("cancel")} >
                        Cancel
                    </Button>
                    <Button disabled={!canExecuteAction("resend")} 
                            onClick={() => executeAction("resend")} 
                            className={styles.invoiceListToolbarButtonSpace}>
                        Resend
                    </Button>
                    <Button disabled={!canExecuteAction("publish")} 
                            onClick={() => executeAction("publish")} 
                            className={styles.invoiceListToolbarButtonSpace}>
                        Publish
                    </Button>
                    <Button disabled={!canExecuteAction("save")} 
                            onClick={() => executeAction("save")} 
                            className={styles.invoiceListToolbarButtonSpace}>
                        Save
                    </Button>
                </Toolbar>
            </div>
            <div className={styles.invoiceListToolbarStatusLine}>
                {errorMessage ? <ErrorControl>{errorMessage}</ErrorControl> : null}
                {infoMessage ? <Info>{infoMessage}</Info> : null}
            </div>
        </div>
    )
});