import { IInvoicePositionDto, IInvoiceDto } from "./IInvoiceDto"
import { v4 as createKey } from 'uuid';

export const defaultInvoiceId = "00000000-0000-0000-0000-000000000000";

export interface IEntityWithKey {
    key: string
}

export interface IInvoicePosition extends IInvoicePositionDto, IEntityWithKey {        
}

//export const defaultPosition : IInvoicePosition = { key: createKey(), ordinal: 1, name: "", description: "", quantity: "0", unitPrice: "0", amount: "0"  };

export interface IInvoice extends IEntityWithKey {

    // Уникальный идентификатор инвойса.
    key: string, 

    // Отображаемый номер
    number: string, 

    // Почта получателя
    receiverEmail: string, 

    // Имя получателя
    receiverName: string,     

    // Дата создания инвойса
    createdAt: string, 

    // Статус инвойса.
    state: number, 

    // Сумма по инвойсу.
    amount: string, 

    // Валюта.
    currency: string,

    // Идентификатор мерчанта.
    merchantId: number,

    // Заметки.
    notes: string,

    // Ссылка для оплаты.
    shareLink: string,

    // Действителен до.
    validTill: string,

    positions: Array<IInvoicePosition>;

    toInvoiceDto(): IInvoiceDto;    

    addPosition(): void;
    delete( key: string ): void;
    moveUp( key: string ): void;
    moveDown( key: string ): void;
}

