import {makeAutoObservable, flow, runInAction} from "mobx";
import isEmpty from "lodash/isEmpty";
import has from "lodash/has";
import { v4 as uuidv4 } from 'uuid';

import {RequestService} from "services/RequestService";

import {getExceptionMessage} from 'utils/errors';

import {object} from "zod";
import { IInvoice, defaultInvoiceId } from "./interfaces/IInvoice";
import { IEntityDataSource } from "./interfaces/IEntityDataSource";


import { InvoiceState } from "./InvoiceStates";
import { Invoice } from "../StoreService/models/Invoice"
import { IProfileStore } from "./interfaces/IProfileStore";

import { CEXPAY_PAYTOOLS_INVOICE_API_URL } from "../../constants";
import {IMerchantStore} from "./interfaces/IMerchantStore";
import moment from "moment";

export class InvoiceStore implements IEntityDataSource<IInvoice> {

    private readonly request: RequestService;

    private readonly profileStore: IProfileStore;

    private  readonly merchantStore: IMerchantStore;

    public loading: boolean = false;

    public error: string = "";

    private invoice: IInvoice = Invoice.CreateEmpty();

    get isEmpty() {
        return isEmpty(this.invoice);
    }
 
    has(id: string) {
        return has(this.invoice, id);
    }

    get instance() {
        return this.invoice;
    }
 
    private set instance(value) {
        this.invoice = value;
    }

    constructor( request: RequestService, merchantStore: IMerchantStore, profileStore: IProfileStore ) {

        this.request = request;
        this.profileStore = profileStore;
        this.merchantStore = merchantStore;

        makeAutoObservable(this);
    }
   
    fetch = flow(function* (this: InvoiceStore, id: string) {
        
        this.loading = true;
        this.error = "";

        if( id == defaultInvoiceId || id == "" || id == null ) {

            this.instance = Invoice.CreateEmpty();
            this.instance.merchantId = this.merchantStore.current?.id ?? 0;
            this.instance.currency = this.profileStore?.invoicingProfile?.currencyDefault ?? 'USD';
            this.instance.validTill = moment().add( this.profileStore?.invoicingProfile?.defaultValidUntilInDays, 'days' ).format();

            this.loading = false;
            return;
        }

        try
        {
            yield Promise.all([ this.fetchAsync(id) ]);

        } catch (error) {
            this.error = getExceptionMessage(error);
        }
        finally {
            this.loading = false;
        }        
    });

    private static readonly TryLimit: number = 10;

    async fetchAsync( invoiceId: string ) {

        let res : any = null;
        let tryCounter = 0;        
        let lastState = InvoiceState.Create;

        // Если сервис вернул состояние сущности "В обработке", то попробуем подождать, пока она обработается...
        while( tryCounter < InvoiceStore.TryLimit && InvoiceState.isTransitionState( lastState ) ) {
               
            const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
            await dummyDealy(2500);

            res = await this.request.get( `${CEXPAY_PAYTOOLS_INVOICE_API_URL}invoice/get/${invoiceId}`);

            lastState = res?.data.state;
            tryCounter++;
        }

        runInAction(() => {
            if (res?.data) {
                this.instance = new Invoice( res.data );
            }
        });    
      }

    update = flow(function* (this: InvoiceStore, invoice: IInvoice) {

        this.loading = true;
        this.error = "";

        try {

            let res = yield this.request.post(
                `${CEXPAY_PAYTOOLS_INVOICE_API_URL}invoice/update`, invoice.toInvoiceDto()
            );    

            const invoiceId = res?.data.id;

            //  Дожидаемся завершения операции.
            yield Promise.all([ this.fetchAsync(invoiceId) ]);

        } finally {
            this.loading = false;
        }
    });

    publish = flow(function* (this: InvoiceStore) {

        const invoiceId = this.instance.key;

        this.loading = true;
        this.error = "";

        try {
            const res = yield this.request.post(
                `${CEXPAY_PAYTOOLS_INVOICE_API_URL}invoice/publish/${invoiceId}`, {}
            );

            //  Дожидаемся завершения операции.
            yield Promise.all([ this.fetchAsync(invoiceId) ]);
            
        } finally {
            this.loading = false;
        }
    });
 
    send = flow(function* (this: InvoiceStore ) {

        const invoiceId = this.instance.key;

        this.loading = true;
        this.error = "";

        try {
            const res = yield this.request.post( 
                `${CEXPAY_PAYTOOLS_INVOICE_API_URL}invoice/send/${invoiceId}`, {}
            );

            //  Дожидаемся завершения операции.
            yield Promise.all([ this.fetchAsync(invoiceId) ]);

        } finally {
            this.loading = false;
        }
    });

    cancel = flow(function* (this: InvoiceStore ) {

        const invoiceId = this.instance.key;

        this.loading = true;
        this.error = "";

        try {
            const res = yield this.request.post( 
                `${CEXPAY_PAYTOOLS_INVOICE_API_URL}invoice/cancel/${invoiceId}`, {}
            );

            //  Дожидаемся завершения операции.
            yield Promise.all([ this.fetchAsync(invoiceId) ]);

        } finally {
            this.loading = false;
        }
    });    
}
