import { FC, useEffect, useState } from "react";

import { Container } from "components/pages/Invoice/Container";

import { Form } from "components/Forms/Form";
import { Inner as FormFields } from "pages/Demo/containers/Booking/components/Inner";
import { FormControl } from "components/Forms/FormControl";

import { useForm } from "react-hook-form";

import { Label } from "components/Forms/Label";
import { Input } from "components/Forms/Input";
import { useStore } from "providers/StoreProvider";

import {
  InvoicingSettingsFields,
  invoicingSettingsSchema
} from "./invoicingSettingsSchema";
import { zodResolver } from "@hookform/resolvers/zod";
import { Option, Select } from "components/Forms/Select";

import { LogoDropzone } from "components/LogoDropzone";
import { IInvoicingProfile } from "services/StoreService/interfaces/IInvoicingProfile";

import { Error } from "components/Forms/Error";
import { getExceptionMessage } from "utils/errors";
import { observer } from "mobx-react-lite";

export interface IInvoisingSettingsPanel {
  profile?: IInvoicingProfile;
  fireSaveEvent: number;

  pushErrorMessage(message: string): void;

  pushInfoMessage(message: string): void;
}

export const Panel: FC<IInvoisingSettingsPanel> = observer(
  ({ profile, fireSaveEvent, pushInfoMessage, pushErrorMessage }) => {
    // Счётчик запросов на сохранение.
    const [saveRequestCounter, setSaveRequestCounter] =
      useState<number>(fireSaveEvent);

    const { profileStore, invoiceListStore } = useStore();

    const { control, register, reset, formState, handleSubmit } =
      useForm<InvoicingSettingsFields>({
        mode: "onChange",
        resolver: zodResolver(invoicingSettingsSchema)
      });

    const { errors, touchedFields: touched } = formState;

    const onSubmit = async (fields: InvoicingSettingsFields) => {
      if (profile == null) return;

      profile.email = fields?.email;
      profile.name = fields?.name;

      profile.url = fields?.url;
      profile.phone = fields?.phone;
//      profile.gatewayPassphrase = fields?.gatewayPassphrase;
//      profile.gatewayUuid = fields?.gatewayUuid;
      profile.logoResourceUuid = fields?.logoResourceUuid;
      profile.currencyDefault = fields?.currencyDefault;
      profile.defaultValidUntilInDays = fields?.defaultValidUntilInDays;

      try {
        await profileStore.update(profile);

        pushInfoMessage("Settings successfully saved.");
      } catch (ex) {
        pushErrorMessage("Error: " + getExceptionMessage(ex));
      }

      invoiceListStore.needReload = true;
    };

    useEffect(() => {
      if (profile == null) return;

      reset(profile);
    }, [profile?.merchantId]);

    useEffect(() => {
      if (profile == null) return;

      if (fireSaveEvent > saveRequestCounter) {
        setSaveRequestCounter(fireSaveEvent);

        handleSubmit(onSubmit /*, onError */)(); // Выполняем синхронно.
      }
    }, [fireSaveEvent]);

    return (
      <Container>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormFields>
            <FormControl>
              <Label>Logo</Label>
              <LogoDropzone control={control} fieldName={"logoResourceUuid"} />
            </FormControl>
            <FormControl />
            <FormControl>
              <Label>Name</Label>
              <Input placeholder="Enter name" {...register("name")} />
              {
                errors?.name ? (
                  <Error>
                    <span>{errors?.name?.message}</span>
                  </Error>
                ) : null
              }
            </FormControl>
            <FormControl>
              <Label>E-mail</Label>
              <Input placeholder="Enter email" {...register("email")} />
              {
                errors?.email ? (
                  <Error>
                    <span>{errors?.email?.message}</span>
                  </Error>
                ) : null
              }
            </FormControl>
            <FormControl>
              <Label>Url</Label>
              <Input placeholder="Enter url" {...register("url")} />
              {
                errors?.url ? (
                  <Error>
                    <span>{errors?.url?.message}</span>
                  </Error>
                ) : null
              }
            </FormControl>
            <FormControl>
              <Label>Phone</Label>
              <Input placeholder="Enter phone" {...register("phone")} />
              {
                errors?.phone ? (
                  <Error>
                    <span>{errors?.phone?.message}</span>
                  </Error>
                ) : null
              }
            </FormControl>
            <FormControl>
              <Label>Default currency</Label>
              <Select {...register("currencyDefault")}>
                {profileStore?.invoicingProfile?.currencies.map(
                  (currency: string) => (
                    <Option key={currency} value={currency}>
                      {currency}
                    </Option>
                  )
                )}
              </Select>
              {
                errors?.currencyDefault ? (
                  <Error>
                    <span>{errors?.currencyDefault?.message}</span>
                  </Error>
                ) : null
              }
              {profileStore?.invoicingProfile?.currencies.length == 0 ? (
                <Error>
                  <span>Empty list. Please, check merchant instuments.</span>
                </Error>
              ) : null}
            </FormControl>
            <FormControl>
              <Label>Default "valid until" period in days</Label>
              <Input
                placeholder="Enter default valid period in days"
                {...register("defaultValidUntilInDays", {
                  setValueAs: (v) => (v === "" ? undefined : parseInt(v))
                })}
              />
              {
                errors?.defaultValidUntilInDays ? (
                  <Error>
                    <span>{errors?.defaultValidUntilInDays?.message}</span>
                  </Error>
                ) : null
              }
            </FormControl>
          </FormFields>
        </Form>
      </Container>
    );
  }
);
