import { AxiosResponse } from "axios";
import { makeAutoObservable, flow } from "mobx";
import isEmpty from "lodash/isEmpty";
import isArray from "lodash/isArray";

import { RequestService } from "services/RequestService";

import { getExceptionMessage } from "utils/errors";

import {IProfileStore} from "./interfaces/IProfileStore";
import { IInvoicingProfile } from "./interfaces/IInvoicingProfile";

import { CEXPAY_PAYTOOLS_INVOICE_API_URL, GATEWAY_ORIGIN_WHITE_LIST} from "../../constants";
import { CEXPAY_PROCESSING_V3_API_URL } from "../../constants";
import {IMerchantStore} from "./interfaces/IMerchantStore";
import { MerchantProfile } from "./models/MerchantProfile";

interface ICreatedGatewayRequestDto {
  description: string,
  passphrase: string,
  domainWhitelist: string[],
  widget: string,
  callbacks: []
}

interface ICreatedGatewayResultDto {
  createdId: string,
  secret: string
}

interface IMerchantInvoiceProfileUpdateResultDto {

  result: IInvoicingProfile;
  resultCode: number;
  resultDescription: string;
}

export class ProfileStore implements IProfileStore {

  private readonly request: RequestService;

  private  readonly  merchantStore: IMerchantStore;

  private readonly items: Map<number, MerchantProfile> = new Map<number, MerchantProfile>();

  public loading: boolean = false;

  public error: string = "";

  getMerchantProfile() : MerchantProfile | null {
    const merchantId = this.merchantStore.current?.id ?? 0;
    if( merchantId == 0 )
      throw new Error("Incorrect merchant Id.");

    const item = this.items.get(merchantId);
    return item == undefined ? null : item;
  }

  get invoicingProfile(): IInvoicingProfile | undefined {
    const item = this.getMerchantProfile();
    if( item == null )
      return undefined;

    return item.invoicingProfile;
  }

  get isInvoicingEnabled() : boolean {
    const item = this.getMerchantProfile();
    return item?.isInvoicingEnabled ?? false;
  }

  get isEmpty() {

    const item = this.getMerchantProfile();
    if( item == null )
      return true;

    return isEmpty(item.merchantInfo);
  }

  get email() {

    const item = this.getMerchantProfile();
    if( item == null )
      throw new Error("Cannot find merchant profile.");

    return item.merchantInfo.userEmail ?? "";
  }

  get roles() {

    const item = this.getMerchantProfile();
    if( item == null )
      return [];

    const permissions = item.merchantInfo.permissions;

    return isArray(permissions)
      ? permissions.map(({ role }: { role: string }) => role)
      : [];
  }

  constructor( request: RequestService, merchantStore: IMerchantStore ) {
    makeAutoObservable(this);

    this.request = request;
    this.merchantStore = merchantStore;
  }


  enableInvoicing = flow(function* (this: ProfileStore) {
    this.loading = true;
    this.error = "";

    try {

      const merchantId: number = this.merchantStore.current?.id ?? 0;
      if( merchantId == 0 )
        return;

      yield this.enableInvoicingInternal();

      const item = yield this.loadMerchantData( merchantId );
      this.items.set( merchantId, item );

    } catch (ex) {
      this.error = getExceptionMessage(ex);
    } finally {
      this.loading = false;
    }
  });

  async enableInvoicingInternal(): Promise<void> {

      const merchantId: number = this.merchantStore.current?.id ?? 0;
      if( merchantId == 0 )
        return;

      let item: MerchantProfile | undefined = this.items.get( merchantId );
      if( item == null ) {
        item = await this.loadMerchantData( merchantId );
      }

      if( item == null ) {
        throw new Error( "Value is not initialized" );
      }

      if( this.isInvoicingEnabled ) {
        return;
      }

      // Some kind of random passphrase
      const passphrase: string = Math.random().toString(36).substring(2);

      const newGateway: ICreatedGatewayRequestDto = {
        description: `Default gateway for invoicing (${merchantId})`,
        passphrase: passphrase,
        domainWhitelist: GATEWAY_ORIGIN_WHITE_LIST,
        widget: "{ \"colors\": { \"ctaColor\": \"#1CB6C1\", \"backgroundColor\": \"#FFFFFF\", \"mainColor1\": \"#344563\", \"mainColor2\": \"#758EB1\", \"borderLineColor\": \"#E3E7EE\", \"errorColor\": \"#E65069\" } }",
        callbacks: []
      };

      // TODO: where is error/exception handling?
      const createGatewayResult: AxiosResponse<any> = await this.request.post(
          `${CEXPAY_PROCESSING_V3_API_URL}gateway`, newGateway
      );

      const createdGateway = createGatewayResult?.data as ICreatedGatewayResultDto; // TODO: where is data validation?
      console.log(`Merchant #${merchantId}. Default gateway was created.`);

      // Saving configuration in paytools
      const profile: IInvoicingProfile = {
        ...item.invoicingProfile,
        merchantId,
        gatewayPassphrase: passphrase,
        gatewayUuid: createdGateway.createdId
      }

      // TODO: What about first request '/gateway' will completed successfully
      // TODO: and following request will failure?
      // TODO: gatewayPassphrase - will miss unrecoverably

      // TODO: where is error/exception handling?
      const res6: AxiosResponse<any> = await this.request.post(
          `${CEXPAY_PAYTOOLS_INVOICE_API_URL}merchant/profile/update/`, profile
      );

      const resObj: IMerchantInvoiceProfileUpdateResultDto  = res6.data; // TODO: where is data validation?
      if( resObj.resultCode != 0 ) {
        throw new Error( resObj.resultDescription );
      }

      console.log(`Merchant #${merchantId}. Default gateway was set into profile successfully.`);
  }


  disableInvoicing = flow(function* (this: ProfileStore) {
    this.loading = true;
    this.error = "";

    try {

      const merchantId: number = this.merchantStore.current?.id ?? 0;
      if( merchantId == 0 )
        return;

      yield this.disableInvoicingInternal();

      const item = yield this.loadMerchantData( merchantId );
      this.items.set( merchantId, item );

    } catch (ex) {
      this.error = getExceptionMessage(ex);
    } finally {
      this.loading = false;
    }
  });

  async disableInvoicingInternal(): Promise<void> {
    const merchantId: number = this.merchantStore.current?.id ?? 0;
    if( merchantId == 0 )
      return;

    let item: MerchantProfile | undefined = this.items.get( merchantId );
    if( item == null ) {
      return;
    }

    if( !this.isInvoicingEnabled ) {
      return;
    }

    // Saving configuration in paytools
    const profile: IInvoicingProfile = {
      ...item.invoicingProfile,
      merchantId,
      gatewayPassphrase: '',
      gatewayUuid: ''
    }

    // TODO: where is error/exception handling?
    const res6: AxiosResponse<any> = await this.request.post(
        `${CEXPAY_PAYTOOLS_INVOICE_API_URL}merchant/profile/update/`, profile
    );

    const resObj: IMerchantInvoiceProfileUpdateResultDto  = res6.data; // TODO: where is data validation?
    if( resObj.resultCode != 0 ) {
      throw new Error( resObj.resultDescription );
    }
    console.log(`Merchant #${merchantId}. Default gateway was deleted from profile.`);

    const deleteGatewayResult: AxiosResponse<any> = await this.request.delete(
        `${CEXPAY_PROCESSING_V3_API_URL}gateway/${item.invoicingProfile.gatewayUuid}`
    );

    console.log(`Merchant #${merchantId}. Default gateway was deleted.`);

    item.invoicingProfile.gatewayUuid = '';
    item.invoicingProfile.gatewayPassphrase = '';
  }


  async loadMerchantData( merchantId: number ) : Promise<MerchantProfile> {

    // Права мерчанта
    const meResult = await this.request.get(
        `${CEXPAY_PROCESSING_V3_API_URL}merchant/me`
    );

    // Настройки сервиса инвойсов.
    const invoicingProfileResult = await this.request.get(
        `${CEXPAY_PAYTOOLS_INVOICE_API_URL}merchant/profile/get/${merchantId}`
    );

    return new MerchantProfile( merchantId, meResult.data, invoicingProfileResult.data );
  }


  fetch = flow(function* (this: ProfileStore) {

    this.loading = true;
    this.error = "";

    const merchantId = this.merchantStore.current?.id ?? 0;
    if( merchantId == 0 )
      throw new Error("Incorrect merchant Id.");

    console.log(`Merchant ${merchantId} profile is loading...`);

    try {

      // Проверяем, что доступен дефолтный гейтвей.
//      yield this.makeInvoisingGateway();

      const item = yield this.loadMerchantData( merchantId );
      this.items.set( merchantId, item );

      console.log(`Merchant ${merchantId} has currency list: ${item.invoicingProfile?.currencies.length ?? 0}`);

    } catch (ex) {
        this.error = getExceptionMessage(ex);
    } finally {
      this.loading = false;
    }
  });


  update = flow(function* (this: ProfileStore, profile: IInvoicingProfile) {

    this.loading = true;

    const merchantId = profile.merchantId;
    if( merchantId == 0 || merchantId == null )
      throw new Error("Incorrect merchant Id.");

    try {
      const res2 = yield this.request.post(
        `${CEXPAY_PAYTOOLS_INVOICE_API_URL}merchant/profile/update/`, profile 
      );   

      const resObj:IMerchantInvoiceProfileUpdateResultDto  = res2.data;
      if( resObj.resultCode != 0 ) {
        throw new Error( resObj.resultDescription );
      }

      let sourceItem = this.items.get( merchantId );
      if( sourceItem == null )
        return;

      const item = new MerchantProfile( merchantId, sourceItem.merchantInfo, resObj.result );
      this.items.set( merchantId, item );

    } finally {
      this.loading = false;
    }
  });

  get errorResolution(): string {
    if ( this.isValid() ) {
      return "";
    }

    if( this.invoicingProfile == null || this.invoicingProfile.merchantId == 0 ) {
      return "Please wait";
    }

    if( !this.isInvoicingEnabled ) {
      return "Please enable invoicing in Settings.";
    }

    if( this.invoicingProfile?.currencies.length == 0 ) {
      return "Please check merchant instruments";
    }

    return "Please fill the invoicing profile."
  }

  isValid(): boolean {

    if( this.invoicingProfile == null || this.invoicingProfile.merchantId == 0 ) {
      return false;
    }

    if( !this.isInvoicingEnabled ) {
      return false;
    }

    const profile = this.invoicingProfile;

    if( profile.name == null || profile.name.length == 0 ) {
      return false;
    }

    if( profile.email == null || profile.email.length == 0 ) {
      return false;
    }

    if( profile.url == null || profile.url.length == 0 ) {
      return false;
    }

    if( profile.phone == null || profile.phone.length == 0 ) {
      return false;
    }

    if( profile.currencies.length == 0 ) {
      return false;
    }

    return true;
  }

  get validationError(): string {

    if( this.invoicingProfile == null || this.invoicingProfile.merchantId == 0 ) {
      return "Profile is loading...";
    }

    if( !this.isInvoicingEnabled ) {
      return `Invoicing is not enabled.`;
    }

    const profile = this.invoicingProfile;

    if( profile.name == null || profile.name.length == 0 ) {
      return `Field "name" not defined.`;
    }

    if( profile.email == null || profile.email.length == 0 ) {
      return `Field "email" not defined.`;
    }

    if( profile.url == null || profile.url.length == 0 ) {
      return `Field "url" not defined.`;
    }

    if( profile.phone == null || profile.phone.length == 0 ) {
      return `Field "phone" not defined.`;
    }

    if( profile.currencies.length == 0 ) {
      return `Field "currencies" not defined.`;
    }

    return "";
  }

}
