import { makeAutoObservable, flow, runInAction } from "mobx";

import { CancellablePromise, IObservableArray } from "mobx/dist/internal";
import { IEntityDataSource } from "./interfaces/IEntityDataSource";
import { IInvoiceDto } from "./interfaces/IInvoiceDto";

import { v4 as createGuid } from 'uuid';
 
import dummyInvoiceMock from "./mock/invoice.json";

import { InvoiceState } from "./InvoiceStates";
import { defaultInvoiceId, IInvoice } from "./interfaces/IInvoice";
import { Invoice } from "./models/Invoice";
import { IProfileStore } from "./interfaces/IProfileStore";
import {IMerchantStore} from "./interfaces/IMerchantStore";


export class DummyInvoiceStore implements IEntityDataSource<IInvoice> {

    private readonly profileStore: IProfileStore;

    private readonly merchantStore: IMerchantStore;

    public loading: boolean = false;

    public error: string = "";

    private data: IInvoice = new Invoice( DummyInvoiceStore.defaultInvoice );

    private readonly dataSource: Array<IInvoiceDto>;

    constructor( dataSource: IObservableArray<IInvoiceDto>, merchantStore: IMerchantStore, profileStore: IProfileStore ) {
    
        this.dataSource = dataSource;
        this.profileStore = profileStore;
        this.merchantStore = merchantStore;

        makeAutoObservable(this);   
    }

    get instance(): IInvoice {
        return this.data;
    }

    private set instance(value) {
        this.data = value;
    }    

    private static defaultInvoice: IInvoiceDto = dummyInvoiceMock;
    
    publish = flow(function* (this: DummyInvoiceStore) {
        this.loading = true;
        // Ok
        this.loading = false;
    });

    send = flow(function* (this: DummyInvoiceStore) {
        this.loading = true;
        // Ok
        this.loading = false;
    });

    cancel = flow(function* (this: DummyInvoiceStore) {
        this.loading = true;
        // Ok
        this.loading = false;
    });    

    update = flow(function* (this: DummyInvoiceStore, entity: IInvoice) {
        this.loading = true;

        const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
        yield Promise.any([ dummyDealy(2000) ]); 

        if( entity.key == defaultInvoiceId ) {

            const newInvoice = new Invoice( Object.assign( entity.toInvoiceDto(), { id: createGuid(), number: (this.dataSource.length + 1).toString() } ) );
            newInvoice.state = InvoiceState.Created;

            this.instance = newInvoice;

            this.dataSource.push( newInvoice );
        }

        this.loading = false;
    });

    fetch = flow(function* (this: DummyInvoiceStore, id: string) {
        this.loading = true;
        // Ok      

        const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
        yield Promise.any([ dummyDealy(200) ]); 

        if( id == defaultInvoiceId || id == "" || id == null ) {

            this.instance = Invoice.CreateEmpty();
            this.instance.merchantId = this.merchantStore.current?.id ?? 0;
            this.instance.currency = this.profileStore?.invoicingProfile?.currencyDefault ?? 'USD';
        }
        else {
            const index = this.dataSource.findIndex( (elem, i, arr) => elem.id == id );
            const invoiceDto = (index < this.dataSource.length) ? this.dataSource[index] : DummyInvoiceStore.defaultInvoice;

            this.instance = new Invoice( invoiceDto );
        }

        this.loading = false;
    } );


}