import { FC, useEffect } from "react";
import { useHistory } from "react-router";
import { observer } from "mobx-react-lite";

import { Button } from "../Pagination";

import { useStore } from "../../providers/StoreProvider";
import { IMerchant } from "../../services/StoreService/interfaces/IMerchantDto";
import { routes } from "../../routes";

import styles from "./MerchantTitle.module.scss";

export const MerchantTitle: FC = observer(() => {
  const { merchantStore } = useStore();
  const history = useHistory();

  const handlerLogoutClick = (id: number) => {
    merchantStore.logout(id);
    history.push(routes.settingsMerchants);
  };

  // Список родителей
  const getParentMarchants = (merchItem: IMerchant | null) => {
    let items = [];
    if (merchItem != null) {
      do {
        const isRootMerchant = merchItem.id == merchantStore.root?.id;
        items.push({
          id: merchItem.id,
          name: merchItem?.name,
          isRootMerchant: isRootMerchant
        });

        merchItem = merchantStore.getMerchantById(merchItem?.parentId ?? -1);
      } while (merchItem != null);

      items.reverse();
    }

    return items;
  };

  let items = getParentMarchants(merchantStore.current);

  useEffect(() => {
    items = getParentMarchants(merchantStore.current);
  }, [merchantStore.current]);

  return (
    <div className={styles.MerchantTitle}>
      {items.map((item) => (
        <div key={item.id}>
          {item.name}{" "}
          {!item.isRootMerchant ? (
            <Button onClick={() => handlerLogoutClick(item.id)}>Logout</Button>
          ) : null}
        </div>
      ))}
    </div>
  );
});
