import {FC, useEffect, useState} from "react";
import { Controller, useForm } from "react-hook-form";

import {Container} from "components/pages/Invoice/Container";
import { Table, Thead, Tbody, Tr, Th, Td, Tfoot } from "components/Table";
import { Form } from "components/Forms/Form";
import { FormControl } from "components/Forms/FormControl";
import { Inner as FormFields } from "../../pages/Demo/containers/Booking/components/Inner";

import {Button} from "components/Forms/Button";

import {useStore} from "../../providers/StoreProvider";
import {InvoiceFields, invoiceSchema} from "./invoiceSchema";

import {zodResolver} from "@hookform/resolvers/zod";
import {Label} from "../../components/Forms/Label";
import {DateInput} from "../../components/Forms/DateInput";
import {Input} from "../../components/Forms/Input";
import {  IInvoice, IInvoicePosition } from "services/StoreService/interfaces/IInvoice";

// import styles2 from "../../components/Forms/Input/Input.module.scss";

import {Toolbar} from "../../components/pages/Invoices/Toolbar";

import styles from "./Panel.module.scss"
import { observer } from "mobx-react-lite";
import { Link } from "components/Link";
import { Invoice } from "services/StoreService/models/Invoice";
import { Visible } from "components/Visible";
import { Select, Option } from "components/Forms/Select";

import { Error as ErrorControl } from "components/Forms/Error";

import { useFlashMessage } from "hooks";
import { getExceptionMessage } from "utils/errors";
import {IInvoiceDto} from "../../services/StoreService/interfaces/IInvoiceDto";
import {InvoiceState} from "../../services/StoreService/InvoiceStates";

export interface IPanel
{
    invoice: IInvoice,
    fireSaveEvent: number,
    pushErrorMessage(message: string): void,
    pushInfoMessage(message: string): void,
}

export const Panel : FC<IPanel> = observer( ( { fireSaveEvent, invoice, pushInfoMessage, pushErrorMessage } ) => {

    // Счётчик запросов на сохранение.
    const [ saveRequestCounter, setSaveRequestCounter ] = useState<number>(fireSaveEvent);

    const { invoiceStore, profileStore, invoiceListStore } = useStore();

    const { control, register, reset, formState, handleSubmit } = useForm({
        mode: "onChange",
        resolver: zodResolver(invoiceSchema),
    });

    const { errors, touchedFields: touched } = formState;

    const onSubmit = async (fields: any, error: any) => {

        console.log( `Updated invoice ${invoice?.key} to: `, fields );

        invoice.receiverName = fields?.receiverName;
        invoice.receiverEmail = fields?.receiverEmail;
        invoice.amount = fields?.amount.toString();
        invoice.validTill = fields?.validTill;
        invoice.currency = fields?.currency;

        invoice.positions.forEach( (position, index) => {

            let item = fields?.positions[ parseInt(position.key) ];
            position.name = item.name;
            position.quantity = item.quantity.toString();
            position.unitPrice = item.unitPrice.toString();
            position.amount = item.amount.toString();
        } );

        try {
            await invoiceStore.update(invoice);  

            pushInfoMessage("Invoice successfully saved.");
        } catch (ex) {

            pushErrorMessage( 'Error: ' + getExceptionMessage(ex) );
        }
        
// TODO move to model
        invoiceListStore.needReload = true;
    }
 
    useEffect(()=>
    {
        reset(invoice);
    }, [invoice?.key]);


    useEffect(()=>
    {          
        if( fireSaveEvent > saveRequestCounter ) {
            setSaveRequestCounter( fireSaveEvent )

            handleSubmit( onSubmit )(); // Выполняем синхронно.
        }
    }, 
    [fireSaveEvent]);

    const canExecuteAction = (action: string) : boolean => {

        const availableActions = InvoiceState.getAvailableActions( invoiceStore?.instance.state );
        return !invoiceStore.loading && availableActions.includes( "save" );
    }

    const executeAction = async (action: string) => {

        if( action != "position.add" )
            return;

        invoice.addPosition();
    } 

    const executeAction2 = async (action: string, key: string) => {
        
        switch (action) {
            case "delete":
                invoice.delete( key );
                break;

            case "up":
                invoice.moveUp( key );
                break;

            case "down":
                invoice.moveDown( key );
                break;

            default:
                break;
        }
    }

    return (
        <Container>
             <Form onSubmit={handleSubmit(onSubmit)}>
                <FormFields>
                    <FormControl>
                        <Label>Receiver Name</Label>
                        <Input placeholder="Enter name" {...register("receiverName")}  />
                        {/*touched?.receiverName &&*/ errors?.receiverName ? (<ErrorControl><span>{errors?.receiverName?.message}</span></ErrorControl> ) : null}
                    </FormControl>
                    <FormControl>
                        <Label>Receiver Email</Label>
                        <Input placeholder="Enter email" {...register("receiverEmail")}  />
                        {/*touched?.receiverEmail &&*/ errors?.receiverEmail ? (<ErrorControl><span>{errors?.receiverEmail?.message}</span></ErrorControl> ) : null}
                    </FormControl>
                    <FormControl>
                        <Label>Amount</Label>
                        <Input placeholder="Enter amount" {...register("amount")} />
                        {/*touched?.amount &&*/ errors?.amount ? (<ErrorControl><span>{errors?.amount?.message}</span></ErrorControl> ) : null}
                    </FormControl>
                    <FormControl>
                        <Label>Valid untill</Label>
                        <DateInput control={control} fieldName={'validTill'} />
                        {/*touched?.validTill &&*/ errors?.validTill ? (<ErrorControl><span>{errors?.validTill?.message}</span></ErrorControl> ) : null}
                    </FormControl>

                    <FormControl>
                        <Label>Currency</Label>
                        <Select {...register("currency")} >
                            {profileStore?.invoicingProfile?.currencies.map( ( currency: string ) => (
                            <Option key={currency}  value={currency}>
                                {currency}                                
                            </Option> 
                            ))}
                        </Select>
                    </FormControl>                    
                </FormFields>
 
                <Table className={styles.PositionsTable}>            
                    {/* <Caption>General data</Caption> */}
                    <Thead>
                        <Tr>
                            <Th>#</Th>
                            <Th>Name</Th>
                            <Th>Price</Th>
                            <Th>Quantity</Th>
                            <Th>Amount</Th>
                            <Th>Action</Th>
                        </Tr>
                    </Thead>
             
                    {<Tbody>
                        <Visible if={invoice?.positions.length == 0}>
                            <Tr key={"EmptyList"}>
                                <Td colspan={6} className={styles.EmptyListTitle}>
                                    Empty list
                                </Td>
                            </Tr>
                        </Visible>
                        <Visible if={invoice?.positions.length != 0}>
                        { 
                            invoice?.positions.map( (position: IInvoicePosition, index) => (
                            <Tr key={position.key}>
                                <Td>{position.ordinal}</Td>
                                <Td>
                                    <Input placeholder={"Enter name of goods or service"} {...register( 'positions[' + position.key + '].name' ) } />
                                    {
                                        (errors?.positions?.length > 0) ? <ErrorControl><span>{errors?.positions[position.key]?.name?.message}</span></ErrorControl> : null
                                    }
                                    {
                                        (errors?.positions?.length > 0) ? <ErrorControl><span>{errors?.positions[position.key]?.unitPrice?.message}</span></ErrorControl> : null
                                    }
                                    {
                                        (errors?.positions?.length > 0) ? <ErrorControl><span>{errors?.positions[position.key]?.quantity?.message}</span></ErrorControl> : null
                                    }
                                    {
                                        (errors?.positions?.length > 0) ? <ErrorControl><span>{errors?.positions[position.key]?.amount?.message}</span></ErrorControl> : null
                                    }

                                </Td>
                                <Td>
                                    <Input placeholder={"0"} {...register("positions[" + position.key + "].unitPrice")} />
                                </Td>
                                <Td>
                                    <Input placeholder={"0"} {...register("positions[" + position.key + "].quantity")} />
                                </Td>
                                <Td>
                                    <Input placeholder={"0"} {...register("positions[" + position.key + "].amount")} />
                                </Td>
                                <Td>
                                    <Link to={{}} onClick={() => executeAction2('delete', position.key)}>
                                        Del
                                    </Link>
                                    <span> </span>
                                    <Link to={{}} onClick={() => executeAction2('up', position.key)}>
                                        Up
                                    </Link>
                                    <span> </span>
                                    <Link to={{}} onClick={() => executeAction2('down', position.key)}>
                                        Down
                                    </Link>
                                </Td>
                            </Tr>
                            ))
                        }
                        </Visible>
                    </Tbody>}
                </Table>
            </Form>

            <div className={styles.invoiceListToolbar}>                
            <div></div>
            <Toolbar>
                <Button disabled={!canExecuteAction("position.add")}
                        onClick={() => executeAction("position.add")} >
                    Add
                </Button>
            </Toolbar>
        </div>

        </Container>
    );



});
