import { makeAutoObservable, observable, runInAction } from "mobx";
import { IInvoice, IInvoicePosition } from "../interfaces/IInvoice"
import { IInvoiceDto, IInvoicePositionDto } from "../interfaces/IInvoiceDto"

import { v4 as createGuid } from 'uuid';

import dummyInvoiceMock from "../mock/invoice.json";
import emptyInvoiceMock from "../mock/emptyinvoice.json";
import { identity } from "lodash";
import { observer } from "mobx-react-lite";
import { formatAsRangeIndex } from "utils/date";
import moment from "moment";

export class Invoice implements IInvoice {

    public get key(): string { return this.id; }

    public id: string;
    public number: string;
    public receiverEmail: string;
    public receiverName: string;
    public createdAt: string;
    public state: number;
    public amount: string;
    public currency: string;
    public merchantId: number;
    public notes: string;
    public shareLink: string;
    public validTill: string;

    public positions: Array<IInvoicePosition> = [];
    private positionLastKey: number = 0;

    private getNewPositionKey(): string {
        //return `${this.id}_${this.positionLastKey++}`;
        return `${this.positionLastKey++}`;
    }

    constructor( invoice: IInvoiceDto ) {
        makeAutoObservable(this);

        this.id = invoice.id;
        this.number = invoice.number;
        this.receiverEmail = invoice.receiverEmail;
        this.receiverName = invoice.receiverName;
        this.createdAt = invoice.createdAt;
        this.state = invoice.state;
        this.amount = invoice.amount;
        this.currency = invoice.currency;
        this.merchantId = invoice.merchantId;
        this.notes = invoice.notes;
        this.shareLink = invoice.shareLink;
        this.validTill = invoice.validTill;

        if( invoice.positions != null ) {            

            this.positions = invoice.positions.map( item => Object.assign( item, { key: this.getNewPositionKey() } ) );

            this.sortPositions();
            this.updatePositionsOrdinal();
        }
    }

    public static CreateEmpty(): Invoice {
        const emptyInvoiceDto = Object.assign( emptyInvoiceMock, { createdAt: moment().format(), amount: null } );
        return new Invoice( emptyInvoiceDto );
    }

    toInvoiceDto(): IInvoiceDto {

        const dto : IInvoiceDto = {
            id: this.id,
            number: this.number,
            receiverEmail: this.receiverEmail,
            receiverName: this.receiverName,
            createdAt: this.createdAt,
            state: this.state,
            amount: this.amount,
            currency: this.currency,
            merchantId: this.merchantId,
            notes: this.notes,
            shareLink: this.shareLink,
            validTill: moment( this.validTill ).format(),
            positions: this.positions.map( item => {
                // all props except Key.
                const { key, ...position } = item;  
                return position as IInvoicePositionDto;
            } )
        };

        return dto;
    }

    private getLastOrdinal(): number {

        const arr = this.positions.map( i => i.ordinal );
        let maxOrdinal = Math.max( ...arr );
        if( maxOrdinal <= 0)
            maxOrdinal = 0;

        return maxOrdinal;
    }

    private sortPositions() {
        
        this.positions.sort( (a,b) => (a.ordinal > b.ordinal) ? 1 : ((b.ordinal > a.ordinal) ? -1 : 0) );
    }

    private updatePositionsOrdinal() {
        
        for (let index = 0; index < this.positions.length; index++) {
            const element = this.positions[index];
            element.ordinal = index + 1;            
        }
    }

    public addPosition() {

        const newPosition : IInvoicePosition = 
        {
            key: this.getNewPositionKey(),
            ordinal: this.getLastOrdinal() + 1,
            name: "",
            description: "",
            quantity: "",
            unitPrice: "",
            amount: ""
        };

        runInAction(() => {
            this.positions.push( newPosition );
        });
    }

    private swapPositions( index1: number, index2: number )
    {
        const temp = this.positions[ index1 ];            
        this.positions[ index1 ] = this.positions[ index2 ];
        this.positions[ index2 ] = temp;        
    }

    public delete( key?: string ) {

        if(key == null)
            return;

        const index = this.positions.findIndex( p => p.key == key );
        if( index == null )
            return;

        runInAction(() => {
            this.positions.splice( index, 1 );

            this.updatePositionsOrdinal();
        });
    }

    public moveUp( key?: string ) {

        if(key == null)
            return;

        let array = this.positions;

        const index = array.findIndex( p => p.key == key );
        if( index == null || index <= 0 )
            return;

        runInAction(() => {
           
            this.swapPositions( index - 1, index );
            this.updatePositionsOrdinal();
        });
    }

    public moveDown( key?: string ) {

        if(key == null)
            return;

        let array = this.positions;

        const index = array.findIndex( p => p.key == key );
        if( index == null || index > array.length - 2 )
            return;

        runInAction(() => {

            this.swapPositions( index, index + 1 );
            this.updatePositionsOrdinal();
        });
    }

}