import {FC, useEffect, useState} from "react";

import {observer} from "mobx-react-lite";
import {Button} from "components/Forms/Button";
import {Toolbar} from "components/pages/Invoices/Toolbar";

import {ToolbarFilter} from '../Filter';

import styles from './InvoicesToolbar.module.scss'
import { IActionToolbar } from "components/IActionToolbar";

import * as FilterComponent from "components/Filter";
import { Visible } from "components/Visible";
import { Table, Thead, Tbody, Tr, Th, Td, Tfoot } from "components/Table";

import { InvoiceStateFilter } from "./InvoiceStateFilter"
import { InvoiceCurrencyFilter } from "./InvoiceCurrencyFilter"
import {Error as ErrorControl} from "../../../components/Forms/Error";
import {Info} from "../../../components/Forms/Info";

export const InvoicesToolbar : FC<IActionToolbar> = observer( ( { executeAction, canExecuteAction = (action: string) : boolean => true, errorMessage, infoMessage }) => {
    
    const [ isFilterAreaVisible, setIsFilterAreaVisible ] = useState( false );
  
    return (
        <div>
            <div>
                <div  className={styles.invoiceListToolbar}>
                    <ToolbarFilter onToggleFilterArea={(isVisible: boolean) => setIsFilterAreaVisible( isVisible )} />
                    <Toolbar>
                        <Button disabled={!canExecuteAction("create")}
                                onClick={() => executeAction("create")} >
                            Create
                        </Button>
                    </Toolbar>
                </div>
                <div className={styles.invoiceListToolbarStatusLine}>
                    {errorMessage ? <ErrorControl>{errorMessage}</ErrorControl> : null}
                    {infoMessage ? <Info>{infoMessage}</Info> : null}
                </div>
            </div>
            <div>
                <Visible if={isFilterAreaVisible} >
                    <Table className={styles.invoiceListToolbarTable}>
                        <Tbody>
                            <Tr>
                                <Td>States:</Td>
                                <Td><InvoiceStateFilter /></Td>
                            </Tr>
                            <Tr>
                                <Td>Currency:</Td>
                                <Td><InvoiceCurrencyFilter /></Td>
                            </Tr>
                        </Tbody>
                    </Table>
                </Visible>
            </div>
        </div>
    )
});