import { FC } from "react";

import styles from "./TopMenu.module.scss"

import { Visible } from "../Visible"
import { Input } from "../Forms/Input"

import { useQueryFilter } from "../../hooks"

export const TopMenu: FC<any> = ( {children = <div></div>, isSearchStringVisible} ) => {
      
    const queryFilter = useQueryFilter();
    const handleInputChange = (e: any) => {
        queryFilter.setSearchText( e.target.value );
    }

    return (
        <div className={styles.TopMenuToolbar}>
            <div className={styles.TopMenuToolbarEmpty}></div>
            {children}
            <div className={styles.TopMenuToolbarEmpty}></div>
            <div>
                <Visible if={isSearchStringVisible}>
                    <Input placeholder="Search..." onChange={handleInputChange} value={queryFilter.search} />
                </Visible>
            </div>
        </div>
    );
  };

