
import React, { useEffect, useState } from "react";
import { FC } from "react";
import { observer } from "mobx-react-lite";
import { useHistory } from "react-router-dom";

import { Error } from "components/Errors/Error";
import { Loader } from "components/Loader";

import { useStore } from "providers/StoreProvider";
import { useQuery } from "hooks/useQuery";
import { useQueryFilter } from "hooks/useQueryFilter";

import {Header} from "../../components/pages/Invoices/Header"
import {InvoicesTopMenu} from "../../components/pages/Invoices/InvoicesTopMenu"

//import {InvoicesToolbar, MerchantToolbar} from './MerchantToolbar';
import {Panel} from './Panel';

import {Container} from "../../components/pages/Invoices/Container";
import { routes } from "routes";
import { defaultInvoiceId } from "services/StoreService/interfaces/IInvoice";
import { InvoiceState } from "services/StoreService/InvoiceStates";
import {SettingsTopMenu} from "../../components/pages/Settings/SettingsTopMenu";

export const Merchant : FC<any> = observer( () => {


    // Счётчик срабатываний useEffect. Только если
//    const [pageLoadingCounter, setPageLoadingCounter] = useState<number>( 0 );

    const { merchantStore, profileStore } = useStore();
    const queryString = useQuery().toString();
    const queryFilter = useQueryFilter();

    useEffect( () => {
        merchantStore.filter(queryFilter);
    }, [queryString]);

    return (
        <>
            <Header title="Merchants" isBackButtonVisiable={false}/>
            <SettingsTopMenu isSearchStringVisible={true}/>
            <Container>
                <Error error={merchantStore.error ?? profileStore.error}>
                    <Loader loading={merchantStore.loading || profileStore.loading}>
                        <Panel />
                    </Loader>
                </Error>
            </Container>
        </>
    );
});
