import { FC, useState } from "react";

import { Group } from "components/Forms/Group";
import * as Component from "components/Filter";
import { Button } from "components/Forms/Button";
import { useQueryFilter } from "hooks";
import { InvoiceState } from "services/StoreService/InvoiceStates";


export const InvoiceStateFilter : FC = () => {

    const queryFilter = useQueryFilter();

    const isCreated = queryFilter.isStateSelected(InvoiceState.Create) && queryFilter.isStateSelected(InvoiceState.Created);
    const isPublished = queryFilter.isStateSelected(InvoiceState.Publish) && queryFilter.isStateSelected(InvoiceState.Published);
    const isCanceled = queryFilter.isStateSelected(InvoiceState.Cancel) && queryFilter.isStateSelected(InvoiceState.Canceled);
    const isPayed = queryFilter.isStateSelected(InvoiceState.Pay) && queryFilter.isStateSelected(InvoiceState.Payed);

    return (   
        <Component.Range>
            <Group>
                <Button
                    variant="group"
                    active={isCreated}
                    onClick={() => { !isCreated ? queryFilter.addStates([ InvoiceState.Create, InvoiceState.Created ]) : queryFilter.removeStates([ InvoiceState.Create, InvoiceState.Created ])}} >
                    Created
                </Button>
                <Button
                    variant="group"
                    active={isPublished}
                    onClick={() => { !isPublished ? queryFilter.addStates([ InvoiceState.Publish, InvoiceState.Published ]) : queryFilter.removeStates([ InvoiceState.Publish, InvoiceState.Published ])}} >
                    Published
                </Button>
                <Button
                    variant="group"
                    active={isCanceled}
                    onClick={() => { !isCanceled ? queryFilter.addStates([ InvoiceState.Cancel, InvoiceState.Canceled ]) : queryFilter.removeStates([ InvoiceState.Cancel, InvoiceState.Canceled ])}} >
                    Canceled
                </Button>
                <Button
                    variant="group"
                    active={isPayed}
                    onClick={() => { !isPayed ? queryFilter.addStates([ InvoiceState.Pay, InvoiceState.Payed ]) : queryFilter.removeStates([ InvoiceState.Pay, InvoiceState.Payed ])}} >
                    Payed
                </Button>                
            </Group>
        </Component.Range>
    );    
}