/* eslint-disable no-restricted-globals */
import { makeAutoObservable, runInAction } from "mobx";
import { Log, User, UserManager } from "oidc-client";

import { getExceptionMessage } from 'utils/errors'
import { IAuthStore } from "./interfaces/IAuthStore";

import { CEXPAY_IDENTITY_API_URL, OAUTH_CALLBACK_PATH, OAUTH_CLIENT_ID, POST_LOGOUT_REDIRECT_URL } from "../../constants";

//Log.logger = console;
//Log.level = Log.INFO;

export class AuthStore implements IAuthStore {

	private readonly userManager: UserManager;

	public loading: boolean = true;

	public error: string = "";

	constructor() {
		makeAutoObservable(this);

		this.userManager = new UserManager({
			authority: `${CEXPAY_IDENTITY_API_URL}`,
			client_id: `${OAUTH_CLIENT_ID}`,
			redirect_uri: `${window.location.origin}${OAUTH_CALLBACK_PATH}`,
			post_logout_redirect_uri: POST_LOGOUT_REDIRECT_URL.toString(),
			response_type: "code",
			scope: "openid profile api",
			loadUserInfo: true,
			filterProtocolClaims: true,
		});
	}

	async user(): Promise<User | null> {
		try {
			return this.userManager.getUser();
		} catch (ex) {
			this.handleError(ex);
			return null;
		}
	}

	async login(): Promise<void> {
		try {
			await this.userManager.signinRedirect();
		} catch (ex) {
			this.handleError(ex);
		}
	}

	async authorize(): Promise<User | null> {
		try {
			let user = await this.userManager.getUser();

			if (user) return user;

			await this.userManager.signinRedirect();

			return this.userManager.getUser();

		} catch (ex) {
			this.handleError(ex);
			return null;
		}
	}

	async logout(): Promise<void> {
		await this.userManager.removeUser();
		await this.userManager.signoutRedirect();
	}

	handleError(error: any): void {
		runInAction(() => {
			this.error = getExceptionMessage(error)
		});
	}
}
