
export class InvoiceActionName {

    public static readonly CreateAction: string = "create";
    public static readonly SaveAction: string = "save";
    public static readonly EditAction: string = "edit";
    public static readonly PublishAction: string = "publish";
    public static readonly ResendAction: string = "resend";
    public static readonly CancelAction: string = "cancel";
    public static readonly ViewAction: string = "view";
}

export class InvoiceState {

    public static readonly Create: number = 0;
    public static readonly Created: number = 1;   //Publish
    public static readonly Publish: number = 2;   
    public static readonly Published: number = 3;  //Cancel Resend
    public static readonly Cancel: number = 4;
    public static readonly Canceled: number = 5;   //Publish
    public static readonly Pay: number = 6;        
    public static readonly Payed: number = 7;      //!Cancel !Publish                  



    // Преобразует состояние в строку.
    public static toString( state: number ) : string {

        switch ( state )
        {
            //case 1: return "DRAFT";
            //case 2: return "PUBLISHED";
            //case 3: return "DELETED";

            case InvoiceState.Create:       return "Create";
            case InvoiceState.Created:      return "Created";
            case InvoiceState.Publish:      return "Publish";
            case InvoiceState.Published:    return "Published";
            case InvoiceState.Cancel:       return "Cancel";
            case InvoiceState.Canceled:     return "Canceled";
            case InvoiceState.Pay:          return "Pay";
            case InvoiceState.Payed:        return "Payed";            
        }

        return "Create";
    }

    // Статус "обработка".
    public static isTransitionState( state: number ) : boolean {

        return [ InvoiceState.Create, InvoiceState.Publish, InvoiceState.Cancel, InvoiceState.Pay ].includes( state );
    }

    // Список доступных действий для указанного состояния.
    public static getAvailableActions( state: number ) : string[] {

        switch ( state )
        {
            case InvoiceState.Create:       return [ InvoiceActionName.EditAction, InvoiceActionName.SaveAction ];
            case InvoiceState.Created:      return [ InvoiceActionName.PublishAction, InvoiceActionName.EditAction, InvoiceActionName.SaveAction ];
            case InvoiceState.Publish:      return [];
            case InvoiceState.Published:    return [ InvoiceActionName.ResendAction, InvoiceActionName.CancelAction ];
            case InvoiceState.Cancel:       return [];
            case InvoiceState.Canceled:     return [];
            case InvoiceState.Pay:          return [];
            case InvoiceState.Payed:        return [];
        }

        return [ InvoiceActionName.CreateAction ];
    }
}

