import React from "react";

// import isEqual from "lodash/isEqual";

import { Button } from "components/Forms/Button";
import { DateRangePicker } from "components/DateRangePicker";
import { Group } from "components/Forms/Group";
import * as Component from "components/Filter";
import * as Icons from "components/Icons";

//import * as date from "utils/date";

import { useQueryFilter } from "hooks/useQueryFilter";
import moment, {Moment} from "moment";

export const DateRangeSelector = () => {
  const { fromDate, toDate, handleDateRange } = useQueryFilter();

  const enabled = fromDate && toDate;

  const date = moment();

  const today = date.clone().startOf('date');
  const yesterday = today.clone().subtract(1, 'days');
  const lastWeek = today.clone().subtract(6, 'days');
  const lastMonth = today.clone().subtract(30, 'days');
  const lastHalfYear = today.clone().subtract(6, 'months');
  const lastYear = today.clone().subtract(1, 'years');

  const ifToday = moment(toDate).isSame( today );
  const ifYesterday = moment(fromDate).isSame( yesterday ) && ifToday;
  const ifLastWeek = moment(fromDate).isSame( lastWeek ) && ifToday;
  const ifLastMonth = moment(fromDate).isSame( lastMonth ) && ifToday;
  const ifHalfYear = moment(fromDate).isSame( lastHalfYear ) && ifToday;
  const ifLastYear = moment(fromDate).isSame( lastYear ) && ifToday;
  const ifCustom = enabled && !(ifYesterday || ifLastWeek || ifLastMonth || ifHalfYear || ifLastYear);

  const handleChange = (from: string = '', to: string = '')  => {
    if (from && to) {
      return handleDateRange(
        moment( from ).format(),
        moment( to ).format()
      );
    }
    return handleDateRange();
  };

  return (
    <Component.Range>
      <Group>
        <DateRangePicker
          toDate={toDate}
          fromDate={fromDate}
          onChange={handleChange}
        >
          <Button variant="group" active={ifCustom}>
            <Icons.Factory type="calendar" />
            Custom
          </Button>
        </DateRangePicker>

        <Button
          variant="group"
          active={ifYesterday}
          onClick={() => handleChange(yesterday.format(), today.format())}
        >
          Yesterday
        </Button>
        <Button
          variant="group"
          active={ifLastWeek}
          onClick={() => handleChange(lastWeek.format(), today.format())}
        >
          7D
        </Button>
        <Button
          variant="group"
          active={ifLastMonth}
          onClick={() => handleChange(lastMonth.format(), today.format())}
        >
          30D
        </Button>
        <Button
          variant="group"
          active={ifHalfYear}
          onClick={() => handleChange(lastHalfYear.format(), today.format())}
        >
          6M
        </Button>
        <Button
          variant="group"
          active={ifLastYear}
          onClick={() => handleChange(lastYear.format(), today.format())}
        >
          12M
        </Button>
        <Button
          variant="group"
          active={!enabled}
          onClick={() => handleChange()}
        >
          All
        </Button>
      </Group>
    </Component.Range>
  );
};
