import { makeAutoObservable } from "mobx";

import { RequestService } from "services/RequestService";

import { IRootStore } from "./interfaces/IRootStore";
import { IAuthStore } from "./interfaces/IAuthStore";

import { IPageableListDataSource } from "./interfaces/IPageableListDataSource";
import { IEntityDataSource } from "./interfaces/IEntityDataSource";
import { IInvoice } from "./interfaces/IInvoice";
import { IInvoiceDto } from "./interfaces/IInvoiceDto";
import { IMerchantStore } from "./interfaces/IMerchantStore";
import { IProfileStore } from "./interfaces/IProfileStore";
import { IResourceStore } from "./interfaces/IResourceStore";


import { AuthStore } from "./AuthStore";
import { ErrorStore } from "./ErrorStore";
import { FunnelStore } from "./FunnelStore";
import { GraphStore } from "./GraphStore";
import { MemberStore } from "./MemberStore";
import { MerchantStore } from "./MerchantStore";
import { InvoiceListStore } from "./InvoiceListStore";
import { InvoiceStore } from "./InvoiceStore";
import { OrderListStore } from "./OrderListStore";
import { OrderStore } from "./OrderStore";
import { ProfileStore } from "./ProfileStore";
import { SpecStore } from "./SpecStore";
import { FinanceStatisticsStore } from "./FinanceStatisticsStore";
import { ReconciliationStatementStore } from "./ReconciliationStatementStore";
import { ResourceStore } from "./ResourceStore";
// import { StatisticsStore } from "./StatisticsStore";

    
export class RootStore implements IRootStore {

  public static readonly PageSize: number = 20;

  private readonly request: RequestService;

  public readonly authStore: IAuthStore;

//  public readonly errorStore: ErrorStore;

//  public readonly financeStatisticsStore: FinanceStatisticsStore;

//  public readonly funnelStore: FunnelStore;

//  public readonly memberStore: MemberStore;

// Список мерчантов.
  public readonly merchantStore: IMerchantStore;

//  public readonly orderStore: OrderStore;

//  public readonly orderListStore: OrderListStore;

  public readonly invoiceListStore: IPageableListDataSource<IInvoiceDto>;

  public readonly invoiceStore: IEntityDataSource<IInvoice>;

  public readonly profileStore: IProfileStore;

  // public readonly statisticsStore: StatisticsStore;

//  public readonly specStore: SpecStore;

//  public readonly reconciliationStatementStore: ReconciliationStatementStore;
 
//  public readonly graphStore: GraphStore;

  public readonly resourceStore: IResourceStore;

  constructor() {

    makeAutoObservable(this);

    this.request = new RequestService();

    this.authStore = new AuthStore();

    this.resourceStore = new ResourceStore( this.request );

//    this.errorStore = new ErrorStore();
    
//    this.financeStatisticsStore = new FinanceStatisticsStore( this.request );

//    this.funnelStore = new FunnelStore( this.request);

//    this.graphStore = new GraphStore( this.request);

//    this.memberStore = new MemberStore( this.request);

//    this.orderStore = new OrderStore( this.request);

//    this.orderListStore = new OrderListStore( this.request);

    this.merchantStore = new MerchantStore( this.request );

    this.profileStore = new ProfileStore( this.request, this.merchantStore );

    this.invoiceStore = new InvoiceStore( this.request, this.merchantStore, this.profileStore );

    this.invoiceListStore = new InvoiceListStore( this.request, this.merchantStore, RootStore.PageSize );

    // this.statisticsStore = new StatisticsStore(this, this.request);

//    this.specStore = new SpecStore( this.request);

//    this.reconciliationStatementStore = new ReconciliationStatementStore( this.request);

    this.onAfterBootstrap();
  }

  onAfterBootstrap() {
    this.request.beforeRequest(async (config: any) => {
      try {
        const user = await this.authStore.user();

        if (user?.access_token) {
          config.headers["Authorization"] = `Bearer ${user?.access_token}`;
        }
      } catch (error) {
        console.error("Error while trying to extract an access_token", error);
      }

      config.headers["CP-IMPERSONATE-MERCHANT-ID"] = this.merchantStore.current?.id;

      return config;
    });

    this.request.afterRequest(
      async (response: any) => {
        return response;
      },
      async (error: any) => {
        if (error?.response?.status === 401) {
          await this.authStore.login();
        } else {
          throw error;
        }
      }
    );
  }
}
