import { TopMenu } from "components/TopMenu";
import { useQuery } from "hooks/useQuery";
import { FC } from "react";
import { routes } from "routes";
import * as Nav from "../../Invoices/Nav"


export const SettingsTopMenu: FC<any> = ( {isSearchStringVisible} ) => {
      
    const querySearch = useQuery().toSearch();

    return (
        <TopMenu isSearchStringVisible={isSearchStringVisible}>
            <Nav.Area>
                <Nav.Link exact={false} to={`${routes.settingsMerchants}`}
                          isActive={(match, { pathname }) =>
                              pathname === routes.settings ||
                              pathname === routes.settingsMerchants ||
                              !!match} >
                Merchant
                </Nav.Link>
                {/*<Nav.Link exact={false} to={`${routes.settingsUsers}`}>*/}
                {/*User*/}
                {/*</Nav.Link>*/}
                <Nav.Link exact={false} to={`${routes.settingsInvoicing}${querySearch}`}
                          isActive={(match, { pathname }) =>
                                                             pathname === routes.settingsInvoicing ||
                                                             !!match} >
                Invoicing
                </Nav.Link>
            </Nav.Area>   
        </TopMenu>
    );
  };