import { z } from "zod";
import moment from "moment";

export const invoicePositionSchema = z.object({
  name: z
    .string()
    .max(512, "String too long.")
    .nonempty({ message: "Please complete Name field." }),

  unitPrice: z
    .string()
    .nonempty({ message: "Please complete Price field." })
    .refine((val) => /^\d+$/.test(val), {
      message: "Price expect nonnegative number.",
    }),

  quantity: z
    .string()
    .nonempty({ message: "Please complete Quantity field." })
    .refine((val) => /^\d+$/.test(val), {
      message: "Quantity expect positive number.",
    }),

  amount: z
    .string()
    .nonempty({ message: "Please complete Amount field." })
    .refine((val) => /^\d+$/.test(val), {
      message: "Amount expect nonnegative number.",
    }),
});

export const invoiceSchema = z.object({
  receiverEmail: z
    .string()
    .regex(
      /^(([^<>()\[\]\\.,;:^#$%|+\s@"]+(\.[^<>()\[\]\\.,;:^#$%|+\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,6}))$/g,
      "Email must be formatted correctly."
    )
    // .email({ message: "Email must be formatted correctly." })
    .max(512, "String too long.")
    .nonempty({ message: "Please complete this required field." }),

  receiverName: z
    .string()
    .max(512, "String too long.")
    .nonempty({ message: "Please complete this required field." }),

  amount: z
    .string()
    .nonempty({ message: "Please complete this required field." })
    .refine((val) => /^\d{1,}(\.\d{1,2})?$/.test(val), {
      message: "Expected nonnegative number.",
    }),


  validTill: z
    .any()
    .refine((date: string) => {
      return !isNaN(Date.parse(date));
    }, "Date required")
    .refine((date) => {
      return moment(date) > moment();
    }, "The date must be after today"),

  // firstname: z.string().optional(),
  // lastname: z.string().optional(),
  // company: z.string().optional(),
  // country: z.string().optional(),
  // business_type: z.string().optional(),

  currency: z.any(),

  positions: z.array(invoicePositionSchema),
});

export type InvoiceFields = z.infer<typeof invoiceSchema>;
