import { flow, makeAutoObservable, observable, runInAction } from "mobx";
import toLower from "lodash/toLower";
import { getExceptionMessage } from "utils/errors";

import { IMerchantStore } from "./interfaces/IMerchantStore";
import merchantJson from "./mock/merchant.json";
import { IMerchant } from "./interfaces/IMerchantDto";
import {
  convertToMerchantTree,
  findByIdInTree,
  flatten,
  sortbyfilter
} from "./MerchantStore";
import { IInvoiceListFilter } from "./interfaces/IInvoiceListFilter";

export class DummyMerchantStore implements IMerchantStore {
  public loading: boolean = true;

  public error: string = "";

  private merchantTree: Array<IMerchant> = [];

  private submerchants: Array<IMerchant> = [];

  private filterfield: IInvoiceListFilter | null = null;

  public currentMerchantId: number = 0;

  constructor() {
    makeAutoObservable(this);
  }

  get isEmpty(): boolean {
    return !this.tree.length;
  }

  get tree(): Array<IMerchant> {
    return this.merchantTree;
  }

  get flattenTree(): Array<IMerchant> {
    return flatten(this.merchantTree)
      .filter(({ enabled, children }: any) => children.length <= 0 && enabled)
      .map(({ id, name }: any) => ({ id, name: toLower(name) }));
  }

  getMerchantById(id: number): IMerchant | null {
    return findByIdInTree(this.tree, id);
  }

  get current(): IMerchant | null {
    return this.getMerchantById(this.currentMerchantId);
  }

  get root(): IMerchant | null {
    if (this.tree.length == 0) return null;

    return this.tree[0];
  }

  setCurrentMerchant(id: number) {
    this.currentMerchantId = id;
    localStorage.setItem("currentMerchantId", id.toString());
    if (this.filterfield != null) this.applyFilter();
  }

  logout(id: number): void {
    const merchant = this.getMerchantById(id);
    if (merchant?.id != this.root?.id) {
      this.setCurrentMerchant(merchant?.parentId ?? this.currentMerchantId);
    }
  }

  fetch = flow(function* (this: DummyMerchantStore) {
    try {
      this.error = "";
      this.loading = true;

      this.merchantTree = convertToMerchantTree(merchantJson);
      const currentMerchantId = Number(
        localStorage.getItem("currentMerchantId")
      );
      this.setCurrentMerchant(currentMerchantId || this.merchantTree[0].id);
    } catch (ex) {
      this.error = getExceptionMessage(ex);
    } finally {
      this.loading = false;
    }
  });

  isMatchFilter(merchant: IMerchant, filter: IInvoiceListFilter | null) {
    if (filter == null) return true;

    if (
      filter.search != "" &&
      !merchant.name.toLowerCase().includes(filter.search.toLowerCase())
    ) {
      return false;
    }

    return true;
  }

  // Список субмерчантов текущего мерчанта
  get currentSubmerchants(): Array<IMerchant> {
    return this.submerchants;
  }

  filter(filter: IInvoiceListFilter) {
    this.filterfield = filter;
    this.applyFilter();
  }

  applyFilter() {
    const filter = this.filterfield;

    runInAction(() => {
      let data =
        this.current?.children.filter((merchant: IMerchant, index, arr) =>
          this.isMatchFilter(merchant, filter)
        ) ?? [];
      data = sortbyfilter(data, filter);

      this.submerchants = observable(data);
    });
  }
}
