import { flow, makeAutoObservable, observable, runInAction } from "mobx";
import toLower from "lodash/toLower";
import { RequestService } from "services/RequestService";

import { getExceptionMessage } from "utils/errors";

import { IMerchantStore } from "./interfaces/IMerchantStore";

import { CEXPAY_PROCESSING_V3_API_URL } from "../../constants";
import { IMerchant } from "./interfaces/IMerchantDto";
import { IInvoiceListFilter } from "./interfaces/IInvoiceListFilter";
import get from "lodash/get";

export class MerchantStore implements IMerchantStore {
  private readonly request: RequestService;

  private submerchants: Array<IMerchant> = [];

  private filterfield: IInvoiceListFilter | null = null;

  currentMerchantId: number = 0;

  public loading: boolean = true;

  public error: string = "";

  private merchants: Array<IMerchant> = [];

  constructor(request: RequestService) {
    makeAutoObservable(this);

    this.request = request;
  }

  get isEmpty(): boolean {
    return !this.tree.length;
  }

  get tree(): Array<IMerchant> {
    return this.merchants;
  }

  get flattenTree(): Array<IMerchant> {
    return flatten(this.merchants)
      .filter(({ enabled, children }: any) => children.length <= 0 && enabled)
      .map(({ id, name }: any) => ({ id, name: toLower(name) }));
  }

  getMerchantById(id: number): IMerchant | null {
    return findByIdInTree(this.tree, id);
  }

  get current(): IMerchant | null {
    return this.getMerchantById(this.currentMerchantId);
  }

  setCurrentMerchant(id: number) {
    this.currentMerchantId = id;
    localStorage.setItem("currentMerchantId", id.toString());
    if (this.filterfield != null) this.applyFilter();
  }

  // Список субмерчантов текущего мерчанта
  get currentSubmerchants(): Array<IMerchant> {
    return this.submerchants;
  }

  logout(id: number): void {
    const merchant = this.getMerchantById(id);
    if (merchant?.id != this.root?.id) {
      this.setCurrentMerchant(merchant?.parentId ?? this.currentMerchantId);
    }
  }

  get root(): IMerchant | null {
    return this.isEmpty ? null : this.tree[0];
  }

  fetch = flow(function* (this: MerchantStore) {
    this.error = "";
    this.loading = true;

    try {
      const res = yield this.request.get(
        `${CEXPAY_PROCESSING_V3_API_URL}merchant`
      );

      if (res?.data) {
        this.merchants = convertToMerchantTree(res.data);
        this.currentMerchantId =
          Number(localStorage.getItem("currentMerchantId")) ||
          this.merchants[0].id;
      }
    } catch (ex) {
      this.error = getExceptionMessage(ex);
    } finally {
      this.loading = false;
    }
  });

  isMatchFilter(merchant: IMerchant, filter: IInvoiceListFilter | null) {
    if (filter == null) return true;

    if (
      filter.search != "" &&
      !merchant.name.toLowerCase().includes(filter.search.toLowerCase())
    ) {
      return false;
    }

    return true;
  }

  filter(filter: IInvoiceListFilter) {
    this.filterfield = filter;
    this.applyFilter();
  }

  applyFilter() {
    const filter = this.filterfield;

    runInAction(() => {
      let data =
        this.current?.children.filter((merchant: IMerchant, index, arr) =>
          this.isMatchFilter(merchant, filter)
        ) ?? [];
      data = sortbyfilter(data, filter);

      this.submerchants = observable(data);
    });
  }
}

export function flatten(list: Array<IMerchant>) {
  const result: any = [];

  for (const merchant of list) {
    const { children } = merchant;

    if (children.length) result.push(...flatten(children));

    result.push(merchant);
  }

  return result;
}

export function findByIdInTree(
  list: Array<IMerchant>,
  id: number
): IMerchant | null {
  for (const item of list) {
    if (item.id == id) {
      return item;
    }
  }

  for (const item of list) {
    const subItem = findByIdInTree(item.children, id);
    if (subItem !== null) {
      return subItem;
    }
  }

  return null;
}

export function convertToMerchantTree(
  items: any,
  parentId: number = -1
): Array<IMerchant> {
  return Array.from<IMerchant>(items).map((item) => {
    const merchant = Object.assign(item, { parentId: parentId });
    merchant.children = convertToMerchantTree(item.children, merchant.id);
    return merchant;
  });
}

export function sortbyfilter<T>(
  array: Array<T>,
  filter: IInvoiceListFilter | null
): Array<T> {
  if (filter == null || filter.sortby == "") {
    return array;
  }

  const field = filter.sortby;
  const order =
    filter.sortDirection == "" || filter.sortDirection == "asc" ? 1 : -1;

  return array.slice().sort((a: any, b: any) => {
    const av = get(a, field);
    const bv = get(b, field);

    if (av > bv) {
      return 1 * order;
    }

    if (av < bv) {
      return -1 * order;
    }

    return 0;
  });
}
