import { makeAutoObservable, flow, runInAction, IObservableArray } from "mobx";
import isEmpty from "lodash/isEmpty";
import get from "lodash/get";

import { getExceptionMessage } from "utils/errors";

import { observable } from 'mobx'


import { IPageableListDataSource } from "./interfaces/IPageableListDataSource";
import { IInvoiceDto } from "./interfaces/IInvoiceDto";
import { IInvoiceListFilter } from "./interfaces/IInvoiceListFilter";
import {IMerchantStore} from "./interfaces/IMerchantStore";
import {sortbyfilter} from "./MerchantStore";
import moment from "moment";

const LIMIT = 10;

// Список инвойсов.
export class DummyInvoiceListStore implements IPageableListDataSource<IInvoiceDto> {

    private readonly merchantStore: IMerchantStore;

    public needReload: boolean = false;

    public loading: boolean = false;

    public error: string = "";
 
    private data: any = {};

    private pageSize: number;

    private readonly dataSource: IObservableArray<IInvoiceDto>;

 
  get totalCount(): number {

    return this.data?.totalCount;
  }

  get all() {
    return this.data?.invoices ?? new Array<IInvoiceDto>();
  }

  constructor( dataSource: IObservableArray<IInvoiceDto>, merchantStore: IMerchantStore, pageSize: number ) {
      makeAutoObservable(this);

      this.dataSource = dataSource;
      this.pageSize = pageSize;
      this.merchantStore = merchantStore;
  }

   isMatchFilter( invoice : IInvoiceDto, filter : IInvoiceListFilter )
   {
        if ( filter.fromDate != '' && moment(filter.fromDate) > moment( invoice.createdAt ) ) {
            return false;
        }

        if( filter.search != '' && ( !invoice.number.toLowerCase().includes( filter.search.toLowerCase() ) &&
                                     !invoice.receiverEmail.toLowerCase().includes( filter.search.toLowerCase() ) ) ) {
            return false;
        }

        if( filter.states?.length != 0 && !filter.states?.includes( invoice.state ) ) {
            return false;
        }

        if( filter.currencies?.length != 0 && !filter.currencies?.includes( invoice.currency ) ) {
          return false;
        }

        return true;
   }
 
  async fetchData( filter : IInvoiceListFilter ) {

      var filterDto =
          {
              merchantId: this.merchantStore.current?.id ?? 0,
              beforeId: '',
              fromDate: filter.fromDate == '' ? null : moment(filter.fromDate).format(),
              toDate: filter.toDate == '' ? null : moment(filter.toDate).format(),
              states: filter.states,
              currencies: filter.currencies,
              sort: filter.sortby,
              dir: filter.sortDirection,
              start: this.pageSize * (filter.page == '' ? 0 : (parseInt( filter.page ) - 1) ),
              limit: this.pageSize,
              search: filter.search
          };

    const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
    await dummyDealy(100);
 
    runInAction(() => {
      let data = this.dataSource.filter( ( invoice : IInvoiceDto, index, arr ) => this.isMatchFilter(invoice, filter) );
      data = sortbyfilter( data, filter );

      this.data = { invoices: observable( data ), totalCount: 1000 };
    });
  }
 
  fetch = flow(function* (this: DummyInvoiceListStore, filter : IInvoiceListFilter ) {

    this.loading = true;
    this.error = "";

    try {
      yield Promise.all([ this.fetchData(filter) ]);
    } catch (error) {
      this.error = getExceptionMessage(error);
    } finally {
      this.loading = false;
    }
  });
}
