import React, {FC, useState} from "react";
import { observer } from "mobx-react-lite";

import { Error } from "components/Errors/Error";
import { Loader } from "components/Loader";

import {Header} from "../../components/pages/Invoices/Header"
import {SettingsTopMenu} from "../../components/pages/Settings/SettingsTopMenu"

import {Container} from "../../components/pages/Invoices/Container";
import { useStore } from "providers/StoreProvider";

import { InvoicingToolbar } from "./InvoicingToolbar"

import { Panel } from "./Panel";
import { useFlashMessage } from "hooks";

import styles from "./InvoicingSettings.module.scss"

export const InvoicingSettings: FC<any> = observer(() => {

    const { profileStore } = useStore();

    const [infoMessage, flashInfoMessage] = useFlashMessage(3000);
    const [errorMessage, flashErrorMessage] = useFlashMessage(5000); 


    // Через эту переменную передаёем события нажатия кнопки "Сохранить".
    const [ saveCounter, setSaveCounter ] = useState<number>(0);

    const { resourceStore } = useStore();
    // const { params } = useRouteMatch<any>();

//     useEffect(() =>
//     {
//        profileStore.fetch();
//     }, []);

    const executeAction = async (action: string) => {

        try
        {
            switch (action)
            {
                case "toggleenabled":
                    if( profileStore.isInvoicingEnabled ) {
                        await profileStore.disableInvoicing();
                    }
                    else {
                        await profileStore.enableInvoicing();
                    }
                    break;
                case "save":
                    setSaveCounter(saveCounter + 1);
                    break;                    
            }

        } catch (err) {
            console.log(err);
        }
    } 

    const canExecuteAction = (action: string) : boolean => {

        switch (action)
        {
            case "toggleenabled":
                return !resourceStore.loading && !profileStore.loading;

            case "save":
                return !resourceStore.loading && !profileStore.loading && profileStore.isInvoicingEnabled;
        }

        return true;
    } 


    return (
        <>
            <Header title={'Settings'} />
            <SettingsTopMenu />
            <Container>
                <InvoicingToolbar isInvoicingEnabled={profileStore.isInvoicingEnabled}
                                  executeAction={(action: string) => executeAction(action)}
                                  canExecuteAction={(action: string) : boolean => canExecuteAction(action)} 
                                  errorMessage={errorMessage} infoMessage={infoMessage} />
                <Error error={profileStore.error}>
                    <Loader loading={profileStore.loading}>
                        {profileStore.isInvoicingEnabled
                            ?
                            <Panel profile={profileStore.invoicingProfile} fireSaveEvent={saveCounter}
                                   pushInfoMessage={(msg) => flashInfoMessage(msg)}
                                   pushErrorMessage={(msg) => flashErrorMessage(msg)}/>
                            :
                            <div className={styles.profileNotEnabledDiv}>
                                Profile is not enabled.<br/>
                                To enable the profile, please press Enable button.
                            </div>
                        }
                    </Loader>
                </Error>
            </Container>
        </>
    );
});

