import { FC, useState } from "react";

import { Group } from "components/Forms/Group";
import * as Component from "components/Filter";
import { Button } from "components/Forms/Button";
import { useQueryFilter } from "hooks";

export const InvoiceCurrencyFilter : FC = () => {

    const queryFilter = useQueryFilter();

    const isUsdPressed = queryFilter.isCurrencySelected('USD');
    const isEurPressed = queryFilter.isCurrencySelected('EUR');
    const isRubPressed = queryFilter.isCurrencySelected('RUB');
    const isGbrPressed = queryFilter.isCurrencySelected('GBP');

    return (   
        <Component.Range>
            <Group>
                <Button
                    variant="group"
                    active={isUsdPressed}
                    onClick={() => !isUsdPressed ? queryFilter.addCurrency('USD') : queryFilter.removeCurrency('USD') } >
                    USD
                </Button>
                <Button
                    variant="group"
                    active={isEurPressed}
                    onClick={() => !isEurPressed ? queryFilter.addCurrency('EUR') : queryFilter.removeCurrency('EUR')} >
                    EUR
                </Button>
                <Button
                    variant="group"
                    active={isGbrPressed}
                    onClick={() => !isGbrPressed ? queryFilter.addCurrency('GBP') : queryFilter.removeCurrency('GBP')} >
                    GBP
                </Button>
                <Button
                    variant="group"
                    active={isRubPressed}
                    onClick={() => !isRubPressed ? queryFilter.addCurrency('RUB') : queryFilter.removeCurrency('RUB')} >
                    RUB
                </Button>
            </Group>
        </Component.Range>
    );
};
  