import React, { FC, useState } from "react";

import {Back} from "../../Invoice/Back"
import {Title} from "../../Invoice/Title"

import styles from './Header.module.scss';
import { Visible } from "components/Visible";

export const Header: FC<any> = ( props ) => {

  const { title, isBackButtonVisiable } = props;

  return (
    <div className={styles.Header}>
      <Visible if={isBackButtonVisiable}>
        <Back className={styles.HeaderBack}/>
      </Visible>      
      <Visible if={!isBackButtonVisiable}>
        <div/>
      </Visible>        
      <Title>{title}</Title>
    </div>     
  );
};
