
import {FC} from "react";

import {observer} from "mobx-react-lite";
import {Button} from "components/Forms/Button";
import {Toolbar} from "components/pages/Invoices/Toolbar";

import styles from './InvoicingToolbar.module.scss'
import { IActionToolbar } from "components/IActionToolbar";

import { Error as ErrorControl } from "components/Forms/Error";
import { Info } from "components/Forms/Info";

interface TInvoicingToolbarParams extends IActionToolbar
{
    isInvoicingEnabled: boolean;
}

export const InvoicingToolbar : FC<TInvoicingToolbarParams> = observer(( { isInvoicingEnabled,
                                                                                         executeAction,
                                                                                         canExecuteAction = (action: string) : boolean => true,
                                                                                         errorMessage,
                                                                                         infoMessage } ) => {
      
    return (
        <div>
            <div className={styles.invoicingListToolbar}>                
                <div></div>
                <Toolbar>
                    <Button disabled={!canExecuteAction("toggleenabled")}
                            onClick={() => executeAction("toggleenabled")}

                            >
                        {isInvoicingEnabled ? "Disable" : "Enable"}
                    </Button>
                    <Button disabled={!canExecuteAction("save")}
                            onClick={() => executeAction("save")}
                            className={styles.invoicingSettingsToolbarButtonSpace}
                            >
                        Save
                    </Button>
                </Toolbar>
            </div>
            <div className={styles.invoiceListToolbarStatusLine}>
                {errorMessage ? <ErrorControl>{errorMessage}</ErrorControl> : null}
                {infoMessage ? <Info>{infoMessage}</Info> : null}
            </div>
        </div>
    )
});