/* eslint-disable no-restricted-globals */
import { makeAutoObservable, runInAction } from "mobx";
import { Log, Profile, User, UserManager, UserSettings } from "oidc-client";

import { getExceptionMessage } from '../../utils/errors'
import { IAuthStore } from "./interfaces/IAuthStore";

import dummyUserMock from './mock/user.json';

// Заглушка для сервиса аутентификации.
export class DummyAuthStore implements IAuthStore {

  public loading: boolean = true;

  public error: string = "";

  private dummyUser: User;

  constructor() {
    makeAutoObservable(this);
 
    // Пользователь-заглушка.
    const dummyUserSettings: UserSettings = {
       id_token: dummyUserMock.id_token,
      session_state: dummyUserMock.session_state,
      access_token: dummyUserMock.access_token,
      token_type: dummyUserMock.token_type,
      refresh_token: "", 
      scope: dummyUserMock.scope,
      profile: { 
        s_hash: dummyUserMock.profile.s_hash,
        sid: dummyUserMock.profile.sid,
        sub: dummyUserMock.profile.sub,
        auth_time: dummyUserMock.profile.auth_time,
        idp: dummyUserMock.profile.idp,
        amr: dummyUserMock.profile.amr,
        two_factor_enabled: dummyUserMock.profile.two_factor_enabled,
        user_name: dummyUserMock.profile.user_name,
        email: dummyUserMock.profile.email,
        email_confirmed: dummyUserMock.profile.email_confirmed,
        iss: "",
        aud: "",
        exp: 0,
        iat: 0
      },
      expires_at: dummyUserMock.expires_at,
      state: undefined
    };

    this.dummyUser = new User( dummyUserSettings );
  }

  async user(): Promise<User | null> {
    try {
      return this.dummyUser;
    } catch (ex) {
      this.handleError(ex);
      return null;
    }
  }

  async login(): Promise<void> {
    try {

      const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
      await dummyDealy(100);
 
    } catch (ex) {
      this.handleError(ex);
    }
  }

  async authorize(): Promise<User | null> {
    try {

      const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
      await dummyDealy(100);

      return this.dummyUser;

    } catch (ex) {
      this.handleError(ex);
      return null;
    }
  }

  async logout(): Promise<void> {
//    await this.userManager.removeUser();
//    await this.userManager.signoutRedirect();
  }

  handleError(error: any) : void {
    runInAction(() => {
      this.error = getExceptionMessage(error)
    });
  }
}
