import {DateRangeSelector} from "../../../containers/Filter/Date";
import {FilterValue} from "./FilterValue";

import styles from "./Filter.module.scss"
import classNames from "classnames";

import * as Icons from "components/Icons";

export interface IToolbarFilter {
    onToggleFilterArea( visible: boolean ): void;
}
// Тулбар фильтра с кнопками быстрого выбора диапазона дат и кнопкой раскрытия полного фильтра.
export const ToolbarFilter : FC<IToolbarFilter> = observer( ( { onToggleFilterArea } ) => {

    const [isPressed, handleToggle] = useToggle(false);    

    const handleToggleFilter = () => {
        handleToggle();        

        // Отправляем событие "наверх".
        onToggleFilterArea(!isPressed);
    };

    return (
        <FilterComponent.AreaUp>
            <DateRangeSelector/>
            <ToggleButton active={isPressed} onClick={handleToggleFilter} className={styles.myFilterButton}>
                <Icons.Factory type="filter" />
                <span>Filter</span>                
            </ToggleButton>
            {/* <Visible if={visible}>
                <Select value={queryFilter.status} onChange={handleChange}>
                    <Option value="">All statuses</Option>
                    {invoiceListStore.statuses.map(({name}: any) => (
                        <Option key={name} value={name}>
                            {name}
                        </Option>
                    ))}
                </Select>
                <FilterValue minValue={0} maxValue={10}/>
            </Visible> */}
        </FilterComponent.AreaUp>
    );
});
