import { makeAutoObservable, flow, runInAction } from "mobx";

import { IResourceStore } from "./interfaces/IResourceStore";
import { ResourceStore } from "./ResourceStore";

import { logoImg } from "./mock/dummylogo"


export class DummyResourceStore implements IResourceStore {

    public static readonly dummyResourceId: string = '1662526d-bb5c-419f-92ac-843b7dec6915'

    public loading: boolean = false;

    public error: string = "";

    constructor() {
    
        makeAutoObservable(this);   
    }

    uploadFiles = flow(function* (this: ResourceStore, files: Array<File> ) {

        this.loading = true;

        try
        {
            // Пауза...
            const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
            yield Promise.any([ dummyDealy(200) ]); 
        }
        finally {
            this.loading = false;
        }

        // Ok!
        return [ { uuid: DummyResourceStore.dummyResourceId, mime: "image/png" } ];
    } );

    getResourceUrl( guid: string ): string
    {
        return logoImg;
    }
}