import React, { FC, useState } from "react";
import ReactDatepicker from "react-datepicker";
import isFunction from "lodash/isFunction";

import "react-datepicker/dist/react-datepicker.css";
import "./styles.scss"

export interface IDatepickerProps {
  toDate: string;
  fromDate: string;
  onChange: (from: string, to: string) => void;
}

export const DateRangePicker: FC<IDatepickerProps> = (props) => {
  const { children, fromDate: start, toDate: end, onChange } = props;
  const [ startDate, setStartDate ] = useState<Date | null>( start != '' ? new Date( start ) : null );
  const [ endDate, setEndDate ] = useState<Date | null>( end != '' ? new Date( end ) : null );

  const handleChange = (dates: any) => {
    const [start, end] = dates;

    setStartDate(start);

    setEndDate(end);

    if (isFunction(onChange) && start && end) {
      onChange( start, end );
    }
  };

  return (
    <ReactDatepicker
      selected={startDate}
      startDate={startDate}
      endDate={endDate}
      selectsRange
      monthsShown={2}
      shouldCloseOnSelect={false}
      onChange={handleChange}
      customInput={children}
    />
  );
};
