function ParseUrlFromEnvs(envName: string): URL {
	const urlStr: string | undefined = process.env[envName];
	if (urlStr === undefined) { throw new Error(`An ${envName} is not set`); }
	const url = new URL(urlStr);
	if (url.pathname.endsWith("/")) {
		return url;
	} else {
		const normalizedUrl: URL = new URL(urlStr + "/");
		return normalizedUrl;
	}
}

export const CEXPAY_IDENTITY_API_URL: URL = ParseUrlFromEnvs("REACT_APP_CEXPAY_IDENTITY_API_URL");
export const CEXPAY_PAYTOOLS_INVOICE_API_URL: URL = ParseUrlFromEnvs("REACT_APP_CEXPAY_PAYTOOLS_INVOICE_API_URL");
export const CEXPAY_PROCESSING_V3_API_URL: URL = ParseUrlFromEnvs("REACT_APP_CEXPAY_PROCESSING_V3_API_URL");

export const GATEWAY_ORIGIN_WHITE_LIST: Array<string> = (function () {
	const REACT_APP_GATEWAY_ORIGIN_WHITE_LIST: string | undefined = process.env.REACT_APP_GATEWAY_ORIGIN_WHITE_LIST;
	if (REACT_APP_GATEWAY_ORIGIN_WHITE_LIST === undefined) { throw new Error(`An REACT_APP_GATEWAY_ORIGIN_WHITE_LIST is not set`); }
	let parsedGatewayOriginWhiteList: any;
	try {
		parsedGatewayOriginWhiteList = JSON.parse(REACT_APP_GATEWAY_ORIGIN_WHITE_LIST);
	} catch {
		throw new Error(`Wrong configuration value '${REACT_APP_GATEWAY_ORIGIN_WHITE_LIST}' for REACT_APP_GATEWAY_ORIGIN_WHITE_LIST. Unparsable JSON.`);
	}
	if (!Array.isArray(parsedGatewayOriginWhiteList)) { throw new Error(`Wrong configuration value '${REACT_APP_GATEWAY_ORIGIN_WHITE_LIST}' for REACT_APP_GATEWAY_ORIGIN_WHITE_LIST. Expected arrays of URL origins.`); }
	const urlOriginRegExp: RegExp = /^[a-z0-9:\.\-_]+$/;
	for (const gatewayOriginStr of parsedGatewayOriginWhiteList) {
		if (!urlOriginRegExp.test(gatewayOriginStr)) {
			throw new Error(`Wrong configuration value '${REACT_APP_GATEWAY_ORIGIN_WHITE_LIST}' for REACT_APP_GATEWAY_ORIGIN_WHITE_LIST. Value '${gatewayOriginStr}' is not URL origin.`);
		}
	}
	return parsedGatewayOriginWhiteList;
})();

export const USE_DUMMY_SERVICES: boolean = (function () {
	const REACT_APP_USE_DUMMY_SERVICES: string | undefined = process.env.REACT_APP_USE_DUMMY_SERVICES;
	if (REACT_APP_USE_DUMMY_SERVICES === undefined) { return false; }
	const REACT_APP_USE_DUMMY_SERVICES_LOWER: string = REACT_APP_USE_DUMMY_SERVICES.toLowerCase();
	switch (REACT_APP_USE_DUMMY_SERVICES_LOWER) {
		case "yes":
		case "true":
			return true;
		default:
			return false;
	}
})();

export const OAUTH_CLIENT_ID: string = (function () {
	const REACT_APP_OAUTH_CLIENT_ID: string | undefined = process.env.REACT_APP_OAUTH_CLIENT_ID;
	if (REACT_APP_OAUTH_CLIENT_ID === undefined) { throw new Error(`An REACT_APP_OAUTH_CLIENT_ID is not set`); }
	return REACT_APP_OAUTH_CLIENT_ID;
})();

export const OAUTH_CALLBACK_PATH: string = (function () {
	const REACT_APP_OAUTH_CALLBACK_PATH: string | undefined = process.env.REACT_APP_OAUTH_CALLBACK_PATH;
	if (REACT_APP_OAUTH_CALLBACK_PATH === undefined) { throw new Error(`An REACT_APP_OAUTH_CALLBACK_PATH is not set`); }
	const urlPathRegExpStr = "^\\/[a-z0-9_\\-\\/]+$";
	const urlPathRegExp = new RegExp(urlPathRegExpStr);
	if (!urlPathRegExp.test(REACT_APP_OAUTH_CALLBACK_PATH)) {
		throw new Error(`Wrong value '${REACT_APP_OAUTH_CALLBACK_PATH}' for REACT_APP_OAUTH_CALLBACK_PATH. Expected URL path accroding to '${urlPathRegExpStr}'.`);
	}
	return REACT_APP_OAUTH_CALLBACK_PATH;
})();

export const POST_LOGOUT_REDIRECT_URL: URL = (function () {
	const REACT_APP_POST_LOGOUT_REDIRECT_URL: string | undefined = process.env.REACT_APP_POST_LOGOUT_REDIRECT_URL;
	if (REACT_APP_POST_LOGOUT_REDIRECT_URL === undefined) { throw new Error(`An REACT_APP_POST_LOGOUT_REDIRECT_URL is not set`); }
	try {
		const postLogoutRedirectUrl: URL = new URL(REACT_APP_POST_LOGOUT_REDIRECT_URL);
		return postLogoutRedirectUrl;
	} catch {
		throw new Error(`Wrong value '${REACT_APP_POST_LOGOUT_REDIRECT_URL}' for REACT_APP_POST_LOGOUT_REDIRECT_URL. Expected valid URL.`);
	}
})();

export const VERSION: string = (function () {
	const REACT_APP_VERSION: string | undefined = process.env.REACT_APP_VERSION;
	if (REACT_APP_VERSION === undefined) { throw new Error(`An REACT_APP_VERSION is not set`); }
	return REACT_APP_VERSION;
})();


