import { z } from "zod";

export const invoicingSettingsSchema = z.object({
  name: z
    .string()
    .nonempty({ message: "Please complete this required field." }),

  email: z
    .string()
    .email({ message: "Email must be formatted correctly." })
    .nonempty({ message: "Please complete this required field." }),

  url: z.string().nonempty({ message: "Please complete this required field." }),

  // firstname: z.string().optional(),
  // lastname: z.string().optional(),
  // company: z.string().optional(),
  // country: z.string().optional(),
  // business_type: z.string().optional(),

  phone: z
    .string()
    .regex(
      /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im,
      "Phone number must be correct."
    )
    .nonempty({ message: "Please complete this required field." }),

  gatewayPassphrase: z
    .string()
    .max(64, "The maximum field length is 64 characters.")
    .nonempty({ message: "Please complete this required field." }),

  gatewayUuid: z
    .string()
    .nonempty({ message: "Please complete this required field." }),

  logoResourceUuid: z.any(),

  currencyDefault: z
    .string()
    .nonempty({ message: "Please complete this required field." }),

  defaultValidUntilInDays: z.number().nonnegative("Must be positive"),
});

//      .number()
//      .min(0,{ message: "Amount should be more than zero" }),

export type InvoicingSettingsFields = z.infer<typeof invoicingSettingsSchema>;
