
import { isArray } from "lodash";
import { makeAutoObservable, flow } from "mobx";
import { IProfileStore } from "./interfaces/IProfileStore";
import meJson from "./mock/me.json";
import { IInvoicingProfile } from "./interfaces/IInvoicingProfile";
import invoicingprofileJson from "./mock/invoicingprofile.json"
import {IMerchantStore} from "./interfaces/IMerchantStore";

export class DummyProfileStore implements IProfileStore {

    private readonly merchantStore: IMerchantStore;

    constructor ( merchantStore: IMerchantStore ) {
        makeAutoObservable(this);

        this.merchantStore = merchantStore;
    }


    enableInvoicing = flow(function* (this: DummyProfileStore) {

    });

    disableInvoicing = flow(function* (this: DummyProfileStore) {

    });

    update = flow(function* (this: DummyProfileStore, profile: IInvoicingProfile) {
        
        this.loading = true;

        const dummyDealy = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))
        yield Promise.any([ dummyDealy(2000) ]); 

        this.data = meJson;
        this.loading = false;

    });

    private data: any = {};

    get roles() {
        const permissions = this.data?.permissions;
    
        return isArray(permissions)
          ? permissions.map(({ role }: { role: string }) => role)
          : [];
      }

    public loading: boolean = false;
    public error: string = "";

    get invoicingProfile() : IInvoicingProfile | undefined {
        return invoicingprofileJson;
    }

    get isInvoicingEnabled() : boolean {
        return this.invoicingProfile?.gatewayUuid !== '';
    }

    fetch = flow(function* (this: DummyProfileStore) {

        const merchantId = this.merchantStore.current?.id;

        this.data = meJson;
    });

    get isEmpty(): boolean {
        return !this.data.length;
    }

    get errorResolution(): string {
        if ( this.isValid() ) {
            return "";
        }

        if( this.invoicingProfile == null || this.invoicingProfile.merchantId == 0 ) {
            return "Please wait";
        }

        if( !this.isInvoicingEnabled ) {
            return "Please enable invoicing in Settings.";
        }

        if( this.invoicingProfile?.currencies.length == 0 ) {
            return "Please check merchant instuments";
        }

        return "Please fill the invoicing profile."
    }

    isValid(): boolean {

        if( this.invoicingProfile == null || this.invoicingProfile.merchantId == 0 ) {
            return false;
        }

        if( !this.isInvoicingEnabled ) {
            return false;
        }

        const profile = this.invoicingProfile;

        if( profile.name == null || profile.name.length == 0 ) {
            return false;
        }

        if( profile.email == null || profile.email.length == 0 ) {
            return false;
        }

        if( profile.url == null || profile.url.length == 0 ) {
            return false;
        }

        if( profile.phone == null || profile.phone.length == 0 ) {
            return false;
        }

        if( profile.currencies.length == 0 ) {
            return false;
        }

        return true;
    }

    get validationError(): string {

        if( this.invoicingProfile == null || this.invoicingProfile.merchantId == 0 ) {
            return "Profile is loading...";
        }

        if( !this.isInvoicingEnabled ) {
            return `Invoicing is not enabled.`;
        }

        const profile = this.invoicingProfile;

        if( profile.name == null || profile.name.length == 0 ) {
            return `Field "name" not defined.`;
        }

        if( profile.email == null || profile.email.length == 0 ) {
            return `Field "email" not defined.`;
        }

        if( profile.url == null || profile.url.length == 0 ) {
            return `Field "url" not defined.`;
        }

        if( profile.phone == null || profile.phone.length == 0 ) {
            return `Field "phone" not defined.`;
        }

        if( profile.currencies.length == 0 ) {
            return `Field "currencies" not defined.`;
        }

        return "";
    }

}