import React, {FC, useEffect, useState} from "react";
import { Link as RouterLink, LinkProps, NavLink } from "react-router-dom";
import * as date from 'utils/date';

import { initial, List } from "lodash";

import { Link } from "components/Link";
import { Sortable } from "components/Sortable";
import { Table, Thead, Tbody, Tr, Th, Td, Tfoot } from "components/Table";
import * as Pagination from "components/Pagination";
import * as Nav from "components/Navigation";

import {observer} from "mobx-react-lite";
import {useStore} from "../../providers/StoreProvider";
import {useQuery, useQueryFilter, useSortable} from "../../hooks";
import { useHistory } from "react-router-dom";
import { IInvoiceDto } from "services/StoreService/interfaces/IInvoiceDto";

import {InvoiceState} from "services/StoreService/InvoiceStates"
import { Visible } from "components/Visible";
import { RootStore } from "services/StoreService";

import linkStyles from "../../components/Link/Link.module.scss";
import {Button} from "../../components/Forms/Button";
import styles from "../InvoiceEdit/InvoicesToolbar/InvoicesToolbar.module.scss";

export const Panel : FC = observer(() => {

    const { invoiceListStore } = useStore();

//    const query = useQuery();
    const queryFilter = useQueryFilter();
//    const querySearch = useQuery().toSearch();
//    const history = useHistory();

    const currentPageNumber = parseInt( (queryFilter.page == '' ? "1" : queryFilter.page) );

    let sortInitial : any = {};
    if( queryFilter.sortby != '') {
        sortInitial[queryFilter.sortby] = queryFilter.sortDirection == 'asc' ? 1 : -1;
    }
 
    const [sortable, handleSortable] = useSortable( (field, order) => {

        queryFilter.setSorting( field, order );
    }, sortInitial );

    const handleFieldSort = (field: string) => () => {        
        handleSortable(field);
    };

    const handlePageChange = (action: string, toPage?: number) => {

        queryFilter.handleGotoPage( (toPage ?? 1).toString() );
    }

    return(
        <>
       <Table>
           <Thead>
               <Tr>
                   <Th>
                       <Sortable order={sortable["number"]} 
                                 onClick={handleFieldSort("number")}>
                           Invoice ID
                       </Sortable>
                   </Th>
                   <Th>
                       <Sortable order={sortable["receiver"]} 
                                 onClick={handleFieldSort("receiver")}>
                           Receiver
                       </Sortable>
                   </Th>

                   <Th>
                       <Sortable order={sortable["createdAt"]}
                                 onClick={handleFieldSort("createdAt")}>
                           Created
                       </Sortable>
                   </Th>
                   <Th>
                       <Sortable order={sortable["status"]}
                                 onClick={handleFieldSort("status")}>
                           Status
                       </Sortable>
                   </Th>
                   <Th>
                       <Sortable order={sortable["amount"]}
                                 onClick={handleFieldSort("amount")}>
                           Amount
                       </Sortable>
                   </Th>
                   <Th>Action</Th>
               </Tr>
           </Thead>
  
           <Tbody>
               {  
                    Array.from<IInvoiceDto>( invoiceListStore.all ).map((invoice: IInvoiceDto) => (
                   <Tr key={invoice.id}>
                       <Td>
                           <Link to={`/invoices/view/${invoice.id}`}>
                               {invoice.number}
                           </Link>
                       </Td>
                       <Td>{`${invoice.receiverName} (${invoice.receiverEmail})`}</Td>
                       <Td>{date.format(invoice.createdAt)}</Td>
                       <Td>{InvoiceState.toString(invoice.state)}</Td>
                       <Td>{invoice.amount} {invoice.currency}</Td>
                       <Td>
                           { InvoiceState.getAvailableActions( invoice.state ).reverse().map( (action)  => {

                               switch (action) {
                                   case "edit":
                                       return (<Link key={invoice.id + "_edit"} to={`/invoices/edit/${invoice.id}`}>Edit</Link>);
                                   //case "publish":
                                   //    return (<a className={linkStyles.link} href={invoice.shareLink} >Share</a>);
                                   //case "resend":
                                   //    return (<Link to={`/invoices/edit/${invoice.id}`}>Resend</Link> );
                                   case "cancel":
                                       return (<a key={invoice.id + "_share"} className={linkStyles.link} href={invoice.shareLink} >Share</a>);
                               }

                               return null;
                           } )
                               //!invoiceStore.loading && availableActions.includes( action );
                               }
                       </Td>
                   </Tr>
                    ))
               }
           </Tbody>

           <Tfoot>
               <Tr>
                   <Td colspan={6}>

                    <Pagination.Component breakLabel="..."
                                          nextLabel=">"
                                          onPageChange={handlePageChange}
                                          currentPageNumber={currentPageNumber}
                                          pageSize={RootStore.PageSize}
                                          itemsTotalCount={invoiceListStore.totalCount}
                                          previousLabel="<" />
                   </Td>
               </Tr>
           </Tfoot>
       </Table>
        </>
   );
});