import React, {FC, useEffect, useState} from "react";
import {observer} from "mobx-react-lite";

import { routes } from "routes";

import { Route, Switch } from "react-router-dom";

import { InvoiceList } from "pages/InvoiceList";
import { Orders } from "pages/Orders";
import { InvoiceView } from "pages/InvoiceView";
import { InvoiceEdit } from "pages/InvoiceEdit";
 
export const Invoices : FC = observer(() => {

    return (
        <Switch>
          <Route exact path={routes.invoiceView} component={InvoiceView} />
          <Route exact path={routes.invoiceEdit} component={InvoiceEdit} />
          <Route>
            <Switch>
              <Route exact path={[routes.invoices, routes.index]} component={InvoiceList} />
              <Route exact path={routes.orders} component={Orders} />
            </Switch>
          </Route>
        </Switch>            
    );
});

