import React, { VFC, FC } from "react";

import { Logo } from "components/Logo";
import { Link } from "components/Link";

import { Typo } from '../Typo';

import styles from './Layout.module.scss';

export const Main: FC = ({children}) => {
  return <section className={styles.main}>{children}</section>
};

export const Inner: FC = ({children}) => {
  return <div className={styles.inner}>{children}</div>
};

export const Header: VFC = () => {
  return <header className={styles.header}><Logo/></header>
};

export const Footer: VFC = () => {
  return (
    <footer className={styles.footer}>
      <Typo.P>Have an account? <Link to='/auth/login' className={styles.link}>Sign in</Link></Typo.P>
    </footer>
  )
};
