import {IInvoicingProfile} from "../interfaces/IInvoicingProfile";
import isEmpty from "lodash/isEmpty";


export class MerchantProfile {

    private readonly merchantid: number;

    get merchantId(): number {
        return this.merchantid;
    }

    private readonly merchantinfo: any = {};

    get merchantInfo(): any {
        return this.merchantinfo;
    }

    private readonly invoicingprofile: IInvoicingProfile;

    get invoicingProfile(): IInvoicingProfile {
        return this.invoicingprofile;
    }

    get isInvoicingEnabled() : boolean {
        return !isEmpty( this.invoicingprofile.gatewayUuid );
    }

    constructor( merchantId: number, merchantInfo: any, invoicingProfile: IInvoicingProfile ) {
        this.merchantid = merchantId;
        this.merchantinfo = merchantInfo;
        this.invoicingprofile = invoicingProfile;
    }
}