import React, {FC} from "react";

import { Sortable } from "components/Sortable";
import { Table, Thead, Tbody, Tr, Th, Td, Tfoot } from "components/Table";

import {observer} from "mobx-react-lite";
import {useStore} from "../../providers/StoreProvider";
import { useQueryFilter, useSortable} from "../../hooks";


import {IMerchantDto} from "../../services/StoreService/interfaces/IMerchantDto";
import {Button} from "components/Pagination";
import styles from "../InvoiceView/Panel.module.scss";


export const Panel : FC = observer(() => {

    const { merchantStore, profileStore } = useStore();
    const queryFilter = useQueryFilter();

    //const currentPageNumber = parseInt( (queryFilter.page == '' ? "1" : queryFilter.page) );

    let sortInitial : any = {};
    if( queryFilter.sortby != '') {
        sortInitial[queryFilter.sortby] = queryFilter.sortDirection == 'asc' ? 1 : -1;
    }
 
    const [sortable, handleSortable] = useSortable( (field, order) => {

        queryFilter.setSorting( field, order );
    }, sortInitial );

    const handleFieldSort = (field: string) => () => {        
        handleSortable(field);
    };

//    const handlePageChange = (action: string, toPage?: number) => {
//
//        queryFilter.handleGotoPage( (toPage ?? 1).toString() );
//    }

    const handlerAuthClick = (id: number) => {
        merchantStore.setCurrentMerchant( id );

        // Переключение профиля происходит через ProfileProvider
    }

    return(
        <>
            <Table className={styles.GeneralDataTable}>
                {/* <Caption>General data</Caption>             */}
                <Tbody>
                    <Tr>
                        <Th>Name</Th>
                        <Th>Description</Th>
                        <Th>Invoicing is enabled</Th>
                    </Tr>
                    <Tr>
                        <Td>{merchantStore.current?.name}</Td>
                        <Td>{merchantStore.current?.description} </Td>
                        <Td>{profileStore.isInvoicingEnabled ? 'Yes' : 'No'}</Td>
                    </Tr>
                </Tbody>
            </Table>




            <div>
                Submerchants:
            </div>
       <Table>
           <Thead>
               <Tr>
                   <Th>
                       <Sortable order={sortable["id"]}
                                 onClick={handleFieldSort("id")}>
                           ID
                       </Sortable>
                   </Th>
                   <Th>
                       <Sortable order={sortable["name"]}
                                 onClick={handleFieldSort("name")}>
                           Name
                       </Sortable>
                   </Th>

                   <Th>
                       <Sortable order={sortable["description"]}
                                 onClick={handleFieldSort("description")}>
                           Description
                       </Sortable>
                   </Th>
                   <Th>
                       Status
                   </Th>
                   <Th>Action</Th>
               </Tr>
           </Thead>

           <Tbody>
               {  
                    Array.from<IMerchantDto>( merchantStore.currentSubmerchants )
                        .map(( merchant: IMerchantDto) => (
                   <Tr key={merchant.id}>
                       <Td>{merchant.id}</Td>
                       <Td>{merchant.name}</Td>
                       <Td>{merchant.description}</Td>
                       <Td>{merchant.enabled ? 'On' : 'Off'}</Td>
                       <Td>
                           <Button onClick={() => handlerAuthClick(merchant.id)}>
                                Auth
                           </Button>
                       </Td>
                   </Tr>
                    ))
               }
           </Tbody>

           <Tfoot>
               <Tr>
                   <Td colspan={6}>

                    {/*<Pagination.Component breakLabel="..."*/}
                    {/*                      nextLabel=">"*/}
                    {/*                      onPageChange={handlePageChange}*/}
                    {/*                      currentPageNumber={currentPageNumber}*/}
                    {/*                      pageSize={RootStore.PageSize}*/}
                    {/*                      itemsTotalCount={invoiceListStore.totalCount}*/}
                    {/*                      previousLabel="<" />*/}
                   </Td>
               </Tr>
           </Tfoot>
       </Table>
        </>
   );
});