import React, { Children, FC } from "react";

import cn from "classnames";

import { Button, IButtonProps } from "components/Forms/Button";

import * as Icons from "components/Icons";

import styles from "./Filter.module.scss";

export interface IFilterProps extends IButtonProps {}

export const ToggleButton : FC<IFilterProps> = ( { active, onClick, children, className } ) => {

  return (
    <Button
      variant="inverse"
      active={active}
      className={cn(styles.Filter, className)}
      onClick={onClick}
    >
      {children}
    </Button>
  );
};
