import { flow, makeAutoObservable } from "mobx";
import { RequestService } from "../RequestService"

import { IResourceStore } from "./interfaces/IResourceStore";
import { logoImg } from 'services/StoreService/mock/dummylogo';
import { getExceptionMessage } from "utils/errors";

import { CEXPAY_PAYTOOLS_INVOICE_API_URL } from "../../constants";

export class ResourceStore implements IResourceStore {

    private readonly request: RequestService;

    public loading: boolean = false;

    public error: string = "";

    constructor( request: RequestService ) {
        makeAutoObservable(this);
    
        this.request = request;
      }

      uploadFiles = flow(function* (this: ResourceStore, files: Array<File> ) {

        this.loading = true;
        this.error = "";

        let formData = new FormData();
      
        //Adding files to the formdata
        files.forEach( file => {
            formData.append( "files", file, file.name );    
        });
                
        try {
          const res = yield this.request.post(
            `${CEXPAY_PAYTOOLS_INVOICE_API_URL}resource/add/`, formData 
          );   

          return res.data;

        } catch (ex) {
          this.error = getExceptionMessage(ex);
        } finally {
          this.loading = false;
        }
      });

    getResourceUrl( uuid: string ): string
    {
        return `${CEXPAY_PAYTOOLS_INVOICE_API_URL}resource/${uuid}/getimage/?width=190&height=130`;
    }
}
