import React, { VFC } from "react";
import { Route, Switch, useParams } from "react-router-dom";

import { routes } from "routes";
import { AuthProvider } from "providers/AuthProvider";
import { MerchantProvider } from "providers/MerchantProvider";
import { ProfileProvider } from "providers/ProfileProvider";
import { StoreProvider } from "providers/StoreProvider";
import { Signup } from "pages/Signup";
import { Demo } from "pages/Demo";
import { Settings } from "pages/Settings";
import { Logout } from "pages/Logout";
import { Analytics } from "pages/Analytics";

import { Navigation } from "containers/Navigation";

import { Main } from "components/Main";
import { Layout } from "components/Layout";
import { NotFoundError } from "components/Errors/NotFoundError";
import {Invoices} from "pages/Invoices";
import {InvoiceView} from "../../pages/InvoiceView";
import {InvoiceEdit} from "../../pages/InvoiceEdit";


// export default function InvoicePage({match}) {
//     let { id } = match.params;
// }

export const Plain: VFC = () => {
  return (
    <Switch>
      <Route path={routes.signup} exact component={Signup}/>
    </Switch>
  )
};

export const Full: VFC = () => {
  return (
    <StoreProvider>
       <AuthProvider>
        <MerchantProvider>
          <ProfileProvider> 
            <Main>
              <Navigation/>
              <Layout>
                <Switch>
                  <Route path={routes.direct} exact component={Demo}/>
                  <Route path={[routes.settings,
                                routes.settingsMerchants,
                                routes.settingsInvoicing
                               ]} exact component={Settings}/>
                  <Route path={routes.logout} exact component={Logout}/>
                  <Route  exact 
                          path={[
                              routes.index,
                              routes.invoices,
                              routes.invoiceEdit,
                              routes.invoiceView
                          ]}                    
                          component={Invoices}/>

                  <Route
                    exact
                    path={[

                      routes.analytics,
                      routes.financestatistics,
                      routes.funnel,
                      routes.order,
                      routes.orders,
                      routes.reconciliationstatement
                    ]}
                    component={Analytics}
                  />
                  <Route path={routes.demo} exact component={Demo}/>                  
                  <Route component={NotFoundError}/>
                </Switch>
              </Layout>
            </Main>
           </ProfileProvider> 
        </MerchantProvider> 
      </AuthProvider> 
    </StoreProvider>
  )
};
