import React, { FC, useState } from "react";

import cn from "classnames"
import styles from "./Button.module.scss";

export interface IPaginationButtonProps {
  disabled?: boolean;
  active?: boolean;
  onClick: (evt: React.SyntheticEvent<HTMLButtonElement>) => void;
}

export const Button: FC<IPaginationButtonProps> = (props) => {
  const { children, disabled = false, active = false, onClick } = props;

  return (
    <button className={cn( styles.Button, (active ? styles.ButtonActive : '') )} disabled={disabled} onClick={onClick}>
      {children}
    </button>
  );
};
